import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function LoginPage() {
  const [form, setForm] = useState({ username: "", password: "" });
  const [loading, setLoading] = useState(false);
  const [errorMsg, setErrorMsg] = useState(null);
  const navigate = useNavigate();

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrorMsg(null);
    setLoading(true);
    try {
      const res = await fetch(`${process.env.REACT_APP_API_BASE}/auth/login`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });
      console.log(res)
   
      if (!res.ok) {
        const err = await res.json();
        throw new Error(err.error || "Login failed");
      }
      const data = await res.json();
      localStorage.setItem("userRole", data.user.role);
      localStorage.setItem("username", data.user.username);
      if (data.token) localStorage.setItem("token", data.token);
      navigate("/dashboard");
    } catch (err) {
      setErrorMsg(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{
      minHeight: "100vh",
      background: `
        linear-gradient(rgba(123,34,34,0.3), rgba(169,68,66,0.3)),
        url('/footer-collage-web.jpg')
      `,
      backgroundSize: "cover",
      backgroundPosition: "center",
      backgroundRepeat: "no-repeat",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      fontFamily: "'Segoe UI', Arial, Helvetica, sans-serif",
      position: "relative",
      overflow: "hidden"
    }}>
      
      {/* Main Login Card - Now Transparent */}
      <div style={{
        background: "rgba(255, 255, 255, 0.15)", // Much more transparent
        backdropFilter: "blur(15px)", // Increased blur for better effect
        borderRadius: 20,
        boxShadow: "0 20px 60px rgba(0,0,0,0.2)",
        padding: "3rem 2.5rem 2.5rem 2.5rem",
        width: "100%",
        maxWidth: 420,
        position: "relative",
        zIndex: 2,
        border: "1px solid rgba(255,255,255,0.3)" // Subtle border for definition
      }}>
        {/* Header Section */}
        <div style={{ textAlign: "center", marginBottom: "2rem" }}>
          <div style={{
            width: "80px",
            height: "80px",
            background: "linear-gradient(135deg, #7b2222, #a94442)",
            borderRadius: "50%",
            margin: "0 auto 1rem auto",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            boxShadow: "0 8px 25px rgba(123,34,34,0.4)"
          }}>
            <div style={{
              width: "60px",
              height: "60px",
              background: "#fff",
              borderRadius: "50%",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              fontSize: "24px",
              fontWeight: "bold",
              color: "#7b2222"
            }}>
              
            </div>
          </div>
          <h1 style={{
            color: "#fff", // Changed to white for better contrast
            fontSize: "1.8rem",
            fontWeight: 700,
            margin: "0 0 0.5rem 0",
            letterSpacing: "0.5px",
            textShadow: "2px 2px 4px rgba(0,0,0,0.5)" // Added text shadow
          }}>
            Sri Sringeri Shankar Math
          </h1>
          <p style={{
            color: "#f0f0f0", // Light color for subtitle
            fontSize: "1rem",
            margin: 0,
            fontStyle: "italic",
            textShadow: "1px 1px 2px rgba(0,0,0,0.5)"
          }}>
            Motinagar, Hyderabad
          </p>
        </div>

        {/* Login Form */}
        <form onSubmit={handleSubmit} style={{ width: "100%" }}>
          {errorMsg && (
            <div style={{
              background: "rgba(255,107,107,0.9)", // Semi-transparent error
              color: "#fff",
              padding: "12px 16px",
              borderRadius: 8,
              marginBottom: 20,
              fontSize: "14px",
              textAlign: "center",
              boxShadow: "0 4px 12px rgba(255,107,107,0.3)",
              backdropFilter: "blur(10px)"
            }}>
              {errorMsg}
            </div>
          )}

          <div style={{ marginBottom: "1.5rem" }}>
            <label style={{
              display: "block",
              marginBottom: "8px",
              fontWeight: 600,
              color: "#fff", // White label
              fontSize: "15px",
              textShadow: "1px 1px 2px rgba(0,0,0,0.5)"
            }}>
              Username
            </label>
            <input
              type="text"
              name="username"
              value={form.username}
              onChange={handleChange}
              required
              style={{
                width: "100%",
                padding: "12px 16px",
                border: "2px solid rgba(255,255,255,0.3)",
                borderRadius: 10,
                fontSize: "16px",
                background: "rgba(255,255,255,0.2)", // Transparent input
                backdropFilter: "blur(10px)",
                color: "#333",
                transition: "all 0.3s ease",
                outline: "none",
                boxSizing: "border-box"
              }}
              placeholder="Enter your username"
            />
          </div>

          <div style={{ marginBottom: "2rem" }}>
            <label style={{
              display: "block",
              marginBottom: "8px",
              fontWeight: 600,
              color: "#fff", // White label
              fontSize: "15px",
              textShadow: "1px 1px 2px rgba(0,0,0,0.5)"
            }}>
              Password
            </label>
            <input
              type="password"
              name="password"
              value={form.password}
              onChange={handleChange}
              required
              style={{
                width: "100%",
                padding: "12px 16px",
                border: "2px solid rgba(255,255,255,0.3)",
                borderRadius: 10,
                fontSize: "16px",
                background: "rgba(255,255,255,0.2)", // Transparent input
                backdropFilter: "blur(10px)",
                color: "#333",
                transition: "all 0.3s ease",
                outline: "none",
                boxSizing: "border-box"
              }}
              placeholder="Enter your password"
            />
          </div>

          <button
            type="submit"
            disabled={loading}
            style={{
              width: "100%",
              padding: "14px 20px",
              background: loading 
                ? "rgba(153,153,153,0.8)" 
                : "linear-gradient(90deg, #7b2222, #a94442)",
              color: "#fff",
              border: "none",
              borderRadius: 10,
              fontSize: "16px",
              fontWeight: 700,
              cursor: loading ? "not-allowed" : "pointer",
              transition: "all 0.3s ease",
              boxShadow: "0 6px 20px rgba(123,34,34,0.4)",
              letterSpacing: "0.5px",
              textTransform: "uppercase"
            }}
          >
            {loading ? "Signing In..." : "Sign In"}
          </button>
        </form>

        {/* Footer */}
        <div style={{
          textAlign: "center",
          marginTop: "2rem",
          paddingTop: "1.5rem",
          borderTop: "1px solid rgba(255,255,255,0.3)"
        }}>
          <p style={{
            color: "#f0f0f0", // Light color for footer
            fontSize: "14px",
            margin: 0,
            lineHeight: 1.5,
            textShadow: "1px 1px 2px rgba(0,0,0,0.5)"
          }}>
             {new Date().getFullYear()} Sri Sringeri Shankar Math<br />
            <span style={{ fontSize: "12px", opacity: 0.8 }}>
              Secure Access Portal
            </span>
          </p>
        </div>
      </div>
    </div>
  );
}
