import React, { useState, useEffect } from "react";
import Report from './Report'; // Import the Report component

export default function CombinedReports() {
  const [activeTab, setActiveTab] = useState("financial");

  // Common filter states for Cash and Bank reports
  const [filters, setFilters] = useState({
    startDate: "",
    endDate: "",
    minAmount: "",
    maxAmount: ""
  });

  // Cash Report States
  const [cashData, setCashData] = useState(null);
  const [cashLoading, setCashLoading] = useState(false);
  const [cashError, setCashError] = useState(null);
  const [cashCurrentPage, setCashCurrentPage] = useState(1);
  const [cashActiveSubTab, setCashActiveSubTab] = useState('receipts');

  // Bank Report States
  const [bankData, setBankData] = useState(null);
  const [bankLoading, setBankLoading] = useState(false);
  const [bankError, setBankError] = useState(null);
  const [bankCurrentPage, setBankCurrentPage] = useState(1);
  const [bankActiveSubTab, setBankActiveSubTab] = useState('receipts');

  // Handle filter changes for Cash and Bank reports
  const handleFilterChange = (field, value) => {
    setFilters(prev => ({ ...prev, [field]: value }));
  };

  const clearFilters = () => {
    setFilters({
      startDate: "",
      endDate: "",
      minAmount: "",
      maxAmount: ""
    });
  };

  // Cash Report Functions
  const fetchCashReport = async (page = 1) => {
    setCashLoading(true);
    setCashError(null);
    
    try {
      const queryParams = new URLSearchParams({
        page: page.toString(),
        limit: '10', // Updated to 10 records per page
        ...Object.fromEntries(Object.entries(filters).filter(([_, value]) => value !== ''))
      });

      const response = await fetch(`${process.env.REACT_APP_API_BASE}/reports/cash?${queryParams}`);
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
      
      const result = await response.json();
      setCashData(result);
      setCashCurrentPage(page);
    } catch (err) {
      setCashError(err.message);
      console.error('Cash report fetch error:', err);
    } finally {
      setCashLoading(false);
    }
  };

  // Bank Report Functions
  const fetchBankReport = async (page = 1) => {
    setBankLoading(true);
    setBankError(null);
    
    try {
      const queryParams = new URLSearchParams({
        page: page.toString(),
        limit: '10', // Updated to 10 records per page
        ...Object.fromEntries(Object.entries(filters).filter(([_, value]) => value !== ''))
      });

      const response = await fetch(`${process.env.REACT_APP_API_BASE}/reports/bank?${queryParams}`);
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
      
      const result = await response.json();
      setBankData(result);
      setBankCurrentPage(page);
    } catch (err) {
      setBankError(err.message);
      console.error('Bank report fetch error:', err);
    } finally {
      setBankLoading(false);
    }
  };

  // Effect to fetch data when tab changes
  useEffect(() => {
    if (activeTab === 'cash') {
      fetchCashReport(1);
    } else if (activeTab === 'bank') {
      fetchBankReport(1);
    }
  }, [activeTab]);

  // Styles
  const containerStyle = {
    minHeight: "100vh",
    background: "linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%)",
    padding: "2rem",
    fontFamily: "Inter, Arial, sans-serif"
  };

  const headerStyle = {
    textAlign: "center",
    marginBottom: "2rem",
    color: "#7b2222"
  };

  const navBarStyle = {
    background: "white",
    padding: "1rem",
    borderRadius: "12px",
    boxShadow: "0 4px 16px rgba(0,0,0,0.1)",
    marginBottom: "2rem",
    display: "flex",
    justifyContent: "center",
    gap: "1rem",
    flexWrap: "wrap"
  };

  const getNavButtonStyle = (tab) => ({
    padding: "12px 24px",
    borderRadius: "8px",
    border: activeTab === tab ? "none" : "2px solid #7b2222",
    fontSize: "16px",
    fontWeight: 600,
    cursor: "pointer",
    transition: "all 0.3s ease",
    background: activeTab === tab 
      ? "linear-gradient(90deg, #7b2222 70%, #a94442 100%)" 
      : "transparent",
    color: activeTab === tab ? "#fff" : "#7b2222",
    display: "flex",
    alignItems: "center",
    gap: "8px",
    minWidth: "180px",
    justifyContent: "center"
  });

  const filterStyle = {
    background: "white",
    padding: "1.5rem",
    borderRadius: "12px",
    boxShadow: "0 4px 16px rgba(0,0,0,0.1)",
    marginBottom: "2rem",
    display: "grid",
    gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))",
    gap: "1rem",
    alignItems: "end"
  };

  const inputStyle = {
    padding: "8px 12px",
    border: "1px solid #ddd",
    borderRadius: "6px",
    fontSize: "14px",
    width: "100%",
    boxSizing: "border-box"
  };

  const buttonStyle = {
    padding: "8px 16px",
    borderRadius: "6px",
    border: "none",
    fontWeight: "600",
    cursor: "pointer",
    fontSize: "14px",
    transition: "all 0.3s ease"
  };

  const cardStyle = {
    background: "white",
    padding: "1.5rem",
    borderRadius: "12px",
    boxShadow: "0 4px 16px rgba(0,0,0,0.1)",
    marginBottom: "2rem"
  };

  // Render Cash Report Content (Charts Removed)
  const renderCashReport = () => {
    if (cashLoading) {
      return (
        <div style={{ ...cardStyle, textAlign: "center", padding: "3rem" }}>
          <h2> Loading Cash Report...</h2>
        </div>
      );
    }

    if (cashError) {
      return (
        <div style={{
          ...cardStyle,
          background: "#f8d7da",
          color: "#721c24",
          border: "1px solid #f5c6cb"
        }}>
           Error: {cashError}
        </div>
      );
    }

    if (!cashData) return null;

    return (
      <>
        {/* Summary Cards */}
        <div style={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))",
          gap: "1rem",
          marginBottom: "2rem"
        }}>
          <div style={{
            ...cardStyle,
            background: "linear-gradient(135deg, #7b2222, #a94442)",
            color: "white",
            textAlign: "center"
          }}>
            <h3 style={{ margin: "0 0 0.5rem 0" }}> Cash Receipts</h3>
            <p style={{ margin: 0, fontSize: "1.5rem", fontWeight: "bold" }}>
              {cashData.summary.receiptCount}
            </p>
            <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem", opacity: 0.9 }}>
              {cashData.summary.totalReceiptAmount.toLocaleString("en-IN")}
            </p>
          </div>
          <div style={{
            ...cardStyle,
            background: "linear-gradient(135deg, #dc3545, #c82333)",
            color: "white",
            textAlign: "center"
          }}>
            <h3 style={{ margin: "0 0 0.5rem 0" }}> Cash Payments</h3>
            <p style={{ margin: 0, fontSize: "1.5rem", fontWeight: "bold" }}>
              {cashData.summary.paymentCount}
            </p>
            <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem", opacity: 0.9 }}>
              {cashData.summary.totalPaymentAmount.toLocaleString("en-IN")}
            </p>
          </div>
          <div style={{
            ...cardStyle,
            background: cashData.summary.netAmount >= 0 
              ? "linear-gradient(135deg, #28a745, #20c997)" 
              : "linear-gradient(135deg, #ffc107, #fd7e14)",
            color: "white",
            textAlign: "center"
          }}>
            <h3 style={{ margin: "0 0 0.5rem 0" }}> Net Cash</h3>
            <p style={{ margin: 0, fontSize: "1.5rem", fontWeight: "bold" }}>
              {cashData.summary.netAmount.toLocaleString("en-IN")}
            </p>
            <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.8rem", opacity: 0.8 }}>
              {cashData.summary.netAmount >= 0 ? " Surplus" : " Deficit"}
            </p>
          </div>
        </div>

        {/* Data Tables */}
        <div style={cardStyle}>
          <div style={{ marginBottom: "1rem" }}>
            <button
              onClick={() => setCashActiveSubTab('receipts')}
              style={{
                ...buttonStyle,
                background: cashActiveSubTab === 'receipts' ? "#7b2222" : "#f8f9fa",
                color: cashActiveSubTab === 'receipts' ? "white" : "#7b2222",
                marginRight: "0.5rem"
              }}
            >
               Cash Receipts ({cashData.receipts?.pagination?.totalRecords || 0})
            </button>
            <button
              onClick={() => setCashActiveSubTab('payments')}
              style={{
                ...buttonStyle,
                background: cashActiveSubTab === 'payments' ? "#7b2222" : "#f8f9fa",
                color: cashActiveSubTab === 'payments' ? "white" : "#7b2222"
              }}
            >
               Cash Payments ({cashData.payments?.pagination?.totalRecords || 0})
            </button>
          </div>

          {cashActiveSubTab === 'receipts' && cashData.receipts?.data?.length > 0 && (
            <div style={{ overflowX: "auto" }}>
              <table style={{ width: "100%", borderCollapse: "collapse" }}>
                <thead>
                  <tr style={{ background: "#f8f9fa" }}>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Receipt No</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Date</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Code</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Description</th>
                    <th style={{ padding: "12px", textAlign: "right", borderBottom: "2px solid #dee2e6" }}>Amount</th>
                  </tr>
                </thead>
                <tbody>
                  {cashData.receipts.data.map((receipt, index) => (
                    <tr key={receipt.receipt_no} style={{
                      borderBottom: "1px solid #dee2e6",
                      backgroundColor: index % 2 === 0 ? "#f9f9f9" : "white"
                    }}>
                      <td style={{ padding: "12px", fontWeight: "600" }}>{receipt.receipt_no}</td>
                      <td style={{ padding: "12px" }}>
                        {new Date(receipt.transaction_date).toLocaleDateString()}
                      </td>
                      <td style={{ padding: "12px" }}>
                        <span style={{
                          background: "#e8f5e8",
                          color: "#155724",
                          padding: "2px 6px",
                          borderRadius: "4px",
                          fontSize: "12px",
                          fontWeight: "600"
                        }}>
                          {receipt.code_no}
                        </span>
                      </td>
                      <td style={{ padding: "12px" }}>{receipt.description}</td>
                      <td style={{ padding: "12px", textAlign: "right", fontWeight: "bold", color: "#7b2222" }}>
                        {Number(receipt.amount).toLocaleString("en-IN")}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}

          {cashActiveSubTab === 'payments' && cashData.payments?.data?.length > 0 && (
            <div style={{ overflowX: "auto" }}>
              <table style={{ width: "100%", borderCollapse: "collapse" }}>
                <thead>
                  <tr style={{ background: "#f8f9fa" }}>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Payment No</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Date</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Code</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Description</th>
                    <th style={{ padding: "12px", textAlign: "right", borderBottom: "2px solid #dee2e6" }}>Amount</th>
                  </tr>
                </thead>
                <tbody>
                  {cashData.payments.data.map((payment, index) => (
                    <tr key={payment.payment_no} style={{
                      borderBottom: "1px solid #dee2e6",
                      backgroundColor: index % 2 === 0 ? "#f9f9f9" : "white"
                    }}>
                      <td style={{ padding: "12px", fontWeight: "600" }}>{payment.payment_no}</td>
                      <td style={{ padding: "12px" }}>
                        {new Date(payment.payment_date).toLocaleDateString()}
                      </td>
                      <td style={{ padding: "12px" }}>
                        <span style={{
                          background: "#fff3cd",
                          color: "#856404",
                          padding: "2px 6px",
                          borderRadius: "4px",
                          fontSize: "12px",
                          fontWeight: "600"
                        }}>
                          {payment.code_no}
                        </span>
                      </td>
                      <td style={{ padding: "12px" }}>{payment.description}</td>
                      <td style={{ padding: "12px", textAlign: "right", fontWeight: "bold", color: "#dc3545" }}>
                        {Number(payment.amount).toLocaleString("en-IN")}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}

          {/* Pagination */}
          {((cashActiveSubTab === 'receipts' && cashData.receipts?.pagination?.totalPages > 1) ||
            (cashActiveSubTab === 'payments' && cashData.payments?.pagination?.totalPages > 1)) && (
            <div style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              gap: "0.5rem",
              marginTop: "1rem",
              padding: "1rem"
            }}>
              <button
                onClick={() => fetchCashReport(cashCurrentPage - 1)}
                disabled={cashCurrentPage <= 1}
                style={{
                  ...buttonStyle,
                  background: cashCurrentPage <= 1 ? "#e9ecef" : "#7b2222",
                  color: cashCurrentPage <= 1 ? "#6c757d" : "white",
                  cursor: cashCurrentPage <= 1 ? "not-allowed" : "pointer"
                }}
              >
                 Previous
              </button>
              
              <span style={{ margin: "0 1rem", fontWeight: "600" }}>
                Page {cashCurrentPage} of {
                  cashActiveSubTab === 'receipts' 
                    ? cashData.receipts?.pagination?.totalPages || 1
                    : cashData.payments?.pagination?.totalPages || 1
                }
              </span>
              
              <button
                onClick={() => fetchCashReport(cashCurrentPage + 1)}
                disabled={cashCurrentPage >= (
                  cashActiveSubTab === 'receipts' 
                    ? cashData.receipts?.pagination?.totalPages || 1
                    : cashData.payments?.pagination?.totalPages || 1
                )}
                style={{
                  ...buttonStyle,
                  background: cashCurrentPage >= (
                    cashActiveSubTab === 'receipts' 
                      ? cashData.receipts?.pagination?.totalPages || 1
                      : cashData.payments?.pagination?.totalPages || 1
                  ) ? "#e9ecef" : "#7b2222",
                  color: cashCurrentPage >= (
                    cashActiveSubTab === 'receipts' 
                      ? cashData.receipts?.pagination?.totalPages || 1
                      : cashData.payments?.pagination?.totalPages || 1
                  ) ? "#6c757d" : "white",
                  cursor: cashCurrentPage >= (
                    cashActiveSubTab === 'receipts' 
                      ? cashData.receipts?.pagination?.totalPages || 1
                      : cashData.payments?.pagination?.totalPages || 1
                  ) ? "not-allowed" : "pointer"
                }}
              >
                Next 
              </button>
            </div>
          )}
        </div>
      </>
    );
  };

  // Render Bank Report Content (Charts Removed)
  const renderBankReport = () => {
    if (bankLoading) {
      return (
        <div style={{ ...cardStyle, textAlign: "center", padding: "3rem" }}>
          <h2> Loading Bank Report...</h2>
        </div>
      );
    }

    if (bankError) {
      return (
        <div style={{
          ...cardStyle,
          background: "#f8d7da",
          color: "#721c24",
          border: "1px solid #f5c6cb"
        }}>
           Error: {bankError}
        </div>
      );
    }

    if (!bankData) return null;

    return (
      <>
        {/* Summary Cards */}
        <div style={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))",
          gap: "1rem",
          marginBottom: "2rem"
        }}>
          <div style={{
            ...cardStyle,
            background: "linear-gradient(135deg, #1976d2, #1565c0)",
            color: "white",
            textAlign: "center"
          }}>
            <h3 style={{ margin: "0 0 0.5rem 0" }}> Bank Receipts</h3>
            <p style={{ margin: 0, fontSize: "1.5rem", fontWeight: "bold" }}>
              {bankData.summary.receiptCount}
            </p>
            <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem", opacity: 0.9 }}>
              {bankData.summary.totalReceiptAmount.toLocaleString("en-IN")}
            </p>
          </div>
          <div style={{
            ...cardStyle,
            background: "linear-gradient(135deg, #d32f2f, #c62828)",
            color: "white",
            textAlign: "center"
          }}>
            <h3 style={{ margin: "0 0 0.5rem 0" }}> Bank Payments</h3>
            <p style={{ margin: 0, fontSize: "1.5rem", fontWeight: "bold" }}>
              {bankData.summary.paymentCount}
            </p>
            <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem", opacity: 0.9 }}>
              {bankData.summary.totalPaymentAmount.toLocaleString("en-IN")}
            </p>
          </div>
          <div style={{
            ...cardStyle,
            background: bankData.summary.netAmount >= 0 
              ? "linear-gradient(135deg, #388e3c, #2e7d32)" 
              : "linear-gradient(135deg, #f57c00, #ef6c00)",
            color: "white",
            textAlign: "center"
          }}>
            <h3 style={{ margin: "0 0 0.5rem 0" }}> Net Bank</h3>
            <p style={{ margin: 0, fontSize: "1.5rem", fontWeight: "bold" }}>
              {bankData.summary.netAmount.toLocaleString("en-IN")}
            </p>
            <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.8rem", opacity: 0.8 }}>
              {bankData.summary.netAmount >= 0 ? " Surplus" : " Deficit"}
            </p>
          </div>
        </div>

        {/* Data Tables */}
        <div style={cardStyle}>
          <div style={{ marginBottom: "1rem" }}>
            <button
              onClick={() => setBankActiveSubTab('receipts')}
              style={{
                ...buttonStyle,
                background: bankActiveSubTab === 'receipts' ? "#1976d2" : "#f8f9fa",
                color: bankActiveSubTab === 'receipts' ? "white" : "#1976d2",
                marginRight: "0.5rem"
              }}
            >
               Bank Receipts ({bankData.receipts?.pagination?.totalRecords || 0})
            </button>
            <button
              onClick={() => setBankActiveSubTab('payments')}
              style={{
                ...buttonStyle,
                background: bankActiveSubTab === 'payments' ? "#1976d2" : "#f8f9fa",
                color: bankActiveSubTab === 'payments' ? "white" : "#1976d2"
              }}
            >
               Bank Payments ({bankData.payments?.pagination?.totalRecords || 0})
            </button>
          </div>

          {bankActiveSubTab === 'receipts' && bankData.receipts?.data?.length > 0 && (
            <div style={{ overflowX: "auto" }}>
              <table style={{ width: "100%", borderCollapse: "collapse" }}>
                <thead>
                  <tr style={{ background: "#f8f9fa" }}>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Receipt No</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Date</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Code</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Description</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Mode</th>
                    <th style={{ padding: "12px", textAlign: "right", borderBottom: "2px solid #dee2e6" }}>Amount</th>
                  </tr>
                </thead>
                <tbody>
                  {bankData.receipts.data.map((receipt, index) => (
                    <tr key={receipt.receipt_no} style={{
                      borderBottom: "1px solid #dee2e6",
                      backgroundColor: index % 2 === 0 ? "#f9f9f9" : "white"
                    }}>
                      <td style={{ padding: "12px", fontWeight: "600" }}>{receipt.receipt_no}</td>
                      <td style={{ padding: "12px" }}>
                        {new Date(receipt.transaction_date).toLocaleDateString()}
                      </td>
                      <td style={{ padding: "12px" }}>
                        <span style={{
                          background: "#e3f2fd",
                          color: "#1976d2",
                          padding: "2px 6px",
                          borderRadius: "4px",
                          fontSize: "12px",
                          fontWeight: "600"
                        }}>
                          {receipt.code_no}
                        </span>
                      </td>
                      <td style={{ padding: "12px" }}>{receipt.description}</td>
                      <td style={{ padding: "12px" }}>
                        <span style={{
                          background: receipt.mode_of_payment === 'cheque' ? "#fff3e0" : "#e8f5e8",
                          color: receipt.mode_of_payment === 'cheque' ? "#f57c00" : "#2e7d32",
                          padding: "2px 6px",
                          borderRadius: "4px",
                          fontSize: "12px",
                          fontWeight: "600"
                        }}>
                          {receipt.mode_of_payment}
                        </span>
                      </td>
                      <td style={{ padding: "12px", textAlign: "right", fontWeight: "bold", color: "#1976d2" }}>
                        {Number(receipt.amount).toLocaleString("en-IN")}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}

          {bankActiveSubTab === 'payments' && bankData.payments?.data?.length > 0 && (
            <div style={{ overflowX: "auto" }}>
              <table style={{ width: "100%", borderCollapse: "collapse" }}>
                <thead>
                  <tr style={{ background: "#f8f9fa" }}>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Payment No</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Date</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Code</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Description</th>
                    <th style={{ padding: "12px", textAlign: "left", borderBottom: "2px solid #dee2e6" }}>Mode</th>
                    <th style={{ padding: "12px", textAlign: "right", borderBottom: "2px solid #dee2e6" }}>Amount</th>
                  </tr>
                </thead>
                <tbody>
                  {bankData.payments.data.map((payment, index) => (
                    <tr key={payment.payment_no} style={{
                      borderBottom: "1px solid #dee2e6",
                      backgroundColor: index % 2 === 0 ? "#f9f9f9" : "white"
                    }}>
                      <td style={{ padding: "12px", fontWeight: "600" }}>{payment.payment_no}</td>
                      <td style={{ padding: "12px" }}>
                        {new Date(payment.payment_date).toLocaleDateString()}
                      </td>
                      <td style={{ padding: "12px" }}>
                        <span style={{
                          background: "#ffebee",
                          color: "#d32f2f",
                          padding: "2px 6px",
                          borderRadius: "4px",
                          fontSize: "12px",
                          fontWeight: "600"
                        }}>
                          {payment.code_no}
                        </span>
                      </td>
                      <td style={{ padding: "12px" }}>{payment.description}</td>
                      <td style={{ padding: "12px" }}>
                        <span style={{
                          background: payment.mode_of_payment === 'cheque' ? "#fff3e0" : "#e8f5e8",
                          color: payment.mode_of_payment === 'cheque' ? "#f57c00" : "#2e7d32",
                          padding: "2px 6px",
                          borderRadius: "4px",
                          fontSize: "12px",
                          fontWeight: "600"
                        }}>
                          {payment.mode_of_payment}
                        </span>
                      </td>
                      <td style={{ padding: "12px", textAlign: "right", fontWeight: "bold", color: "#d32f2f" }}>
                        {Number(payment.amount).toLocaleString("en-IN")}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}

          {/* Pagination */}
          {((bankActiveSubTab === 'receipts' && bankData.receipts?.pagination?.totalPages > 1) ||
            (bankActiveSubTab === 'payments' && bankData.payments?.pagination?.totalPages > 1)) && (
            <div style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              gap: "0.5rem",
              marginTop: "1rem",
              padding: "1rem"
            }}>
              <button
                onClick={() => fetchBankReport(bankCurrentPage - 1)}
                disabled={bankCurrentPage <= 1}
                style={{
                  ...buttonStyle,
                  background: bankCurrentPage <= 1 ? "#e9ecef" : "#1976d2",
                  color: bankCurrentPage <= 1 ? "#6c757d" : "white",
                  cursor: bankCurrentPage <= 1 ? "not-allowed" : "pointer"
                }}
              >
                 Previous
              </button>
              
              <span style={{ margin: "0 1rem", fontWeight: "600" }}>
                Page {bankCurrentPage} of {
                  bankActiveSubTab === 'receipts' 
                    ? bankData.receipts?.pagination?.totalPages || 1
                    : bankData.payments?.pagination?.totalPages || 1
                }
              </span>
              
              <button
                onClick={() => fetchBankReport(bankCurrentPage + 1)}
                disabled={bankCurrentPage >= (
                  bankActiveSubTab === 'receipts' 
                    ? bankData.receipts?.pagination?.totalPages || 1
                    : bankData.payments?.pagination?.totalPages || 1
                )}
                style={{
                  ...buttonStyle,
                  background: bankCurrentPage >= (
                    bankActiveSubTab === 'receipts' 
                      ? bankData.receipts?.pagination?.totalPages || 1
                      : bankData.payments?.pagination?.totalPages || 1
                  ) ? "#e9ecef" : "#1976d2",
                  color: bankCurrentPage >= (
                    bankActiveSubTab === 'receipts' 
                      ? bankData.receipts?.pagination?.totalPages || 1
                      : bankData.payments?.pagination?.totalPages || 1
                  ) ? "#6c757d" : "white",
                  cursor: bankCurrentPage >= (
                    bankActiveSubTab === 'receipts' 
                      ? bankData.receipts?.pagination?.totalPages || 1
                      : bankData.payments?.pagination?.totalPages || 1
                  ) ? "not-allowed" : "pointer"
                }}
              >
                Next 
              </button>
            </div>
          )}
        </div>
      </>
    );
  };

  return (
    <div style={containerStyle}>
      {/* Header */}
      <div style={headerStyle}>
        <h1 style={{ fontSize: "2.5rem", margin: "0 0 0.5rem 0", fontWeight: 800 }}>
           Comprehensive Reports
        </h1>
        <p style={{ fontSize: "1.1rem", margin: 0, opacity: 0.8 }}>
          Financial, Cash and Bank transaction reports
        </p>
      </div>

      {/* Navigation Bar */}
      <div style={navBarStyle}>
        <button
          onClick={() => setActiveTab("financial")}
          style={getNavButtonStyle("financial")}
        >
           Financial Report
        </button>
        
        <button
          onClick={() => setActiveTab("cash")}
          style={getNavButtonStyle("cash")}
        >
           Cash Report
        </button>
        
        <button
          onClick={() => setActiveTab("bank")}
          style={getNavButtonStyle("bank")}
        >
           Bank Report
        </button>
      </div>

      {/* Filter Section - Only show for Cash and Bank reports */}
      {(activeTab === 'cash' || activeTab === 'bank') && (
        <div style={filterStyle}>
          <div>
            <label style={{ display: "block", marginBottom: "0.5rem", fontWeight: "600", color: "#7b2222" }}>
              Start Date:
            </label>
            <input
              type="date"
              value={filters.startDate}
              onChange={(e) => handleFilterChange('startDate', e.target.value)}
              style={inputStyle}
            />
          </div>
          <div>
            <label style={{ display: "block", marginBottom: "0.5rem", fontWeight: "600", color: "#7b2222" }}>
              End Date:
            </label>
            <input
              type="date"
              value={filters.endDate}
              onChange={(e) => handleFilterChange('endDate', e.target.value)}
              style={inputStyle}
            />
          </div>
          <div>
            <label style={{ display: "block", marginBottom: "0.5rem", fontWeight: "600", color: "#7b2222" }}>
              Min Amount:
            </label>
            <input
              type="number"
              value={filters.minAmount}
              onChange={(e) => handleFilterChange('minAmount', e.target.value)}
              placeholder="0"
              style={inputStyle}
            />
          </div>
          <div>
            <label style={{ display: "block", marginBottom: "0.5rem", fontWeight: "600", color: "#7b2222" }}>
              Max Amount:
            </label>
            <input
              type="number"
              value={filters.maxAmount}
              onChange={(e) => handleFilterChange('maxAmount', e.target.value)}
              placeholder="No limit"
              style={inputStyle}
            />
          </div>
          <div style={{ display: "flex", gap: "0.5rem" }}>
            <button
              onClick={() => {
                if (activeTab === 'cash') fetchCashReport(1);
                else if (activeTab === 'bank') fetchBankReport(1);
              }}
              disabled={activeTab === 'cash' ? cashLoading : bankLoading}
              style={{
                ...buttonStyle,
                background: "#7b2222",
                color: "white",
                opacity: (activeTab === 'cash' ? cashLoading : bankLoading) ? 0.6 : 1
              }}
            >
               {(activeTab === 'cash' ? cashLoading : bankLoading) ? 'Loading...' : 'Apply Filters'}
            </button>
            <button
              onClick={() => {
                clearFilters();
                if (activeTab === 'cash') fetchCashReport(1);
                else if (activeTab === 'bank') fetchBankReport(1);
              }}
              style={{
                ...buttonStyle,
                background: "#6c757d",
                color: "white"
              }}
            >
               Clear
            </button>
          </div>
        </div>
      )}

      {/* Report Content */}
      {activeTab === "financial" && <Report />}
      {activeTab === "cash" && renderCashReport()}
      {activeTab === "bank" && renderBankReport()}
    </div>
  );
}
