import React, { useState, useEffect } from "react";

const ALLOWED_CODES = ['R8', 'R13', 'P11', 'P19']; // Only these codes can be managed

export default function CodeMasterAdmin() {
  const [codes, setCodes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(null);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [showAddForm, setShowAddForm] = useState(false);
  const [newCode, setNewCode] = useState({
    code_no: "",
    type: "receipt",
    description: "",
    default_amount: ""
  });

  // Fetch codes from code_master table
  useEffect(() => {
    fetchCodes();
  }, []);

  const fetchCodes = async () => {
    try {
      setLoading(true);
      const response = await fetch(`${process.env.REACT_APP_API_BASE}/codes/allowed-codes`);
      
      if (!response.ok) {
        throw new Error('Failed to fetch codes');
      }
      
      const data = await response.json();
      setCodes(data);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  // Handle field changes
  const handleChange = (id, field, value) => {
    setCodes(prevCodes =>
      prevCodes.map(code =>
        code.id === id ? { ...code, [field]: value } : code
      )
    );
  };

  // Handle new code form changes
  const handleNewCodeChange = (field, value) => {
    setNewCode(prev => ({ ...prev, [field]: value }));
  };

  // Save/Update existing code
  const handleSave = async (id) => {
    const codeToUpdate = codes.find(code => code.id === id);
    if (!codeToUpdate) return;

    if (!codeToUpdate.description.trim()) {
      showMessage("Description cannot be empty", "error");
      return;
    }

    if (codeToUpdate.default_amount && isNaN(Number(codeToUpdate.default_amount))) {
      showMessage("Amount must be a valid number", "error");
      return;
    }

    try {
      setSaving(id);
      const response = await fetch(`${process.env.REACT_APP_API_BASE}/codes/${id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          description: codeToUpdate.description.trim(),
          default_amount: codeToUpdate.default_amount ? Number(codeToUpdate.default_amount) : null
        })
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to update code');
      }

      const updatedCode = await response.json();
      
      // Update state with server response
      setCodes(prevCodes =>
        prevCodes.map(code =>
          code.id === id ? { ...updatedCode, default_amount: updatedCode.default_amount?.toString() || "" } : code
        )
      );

      showMessage(`Code ${codeToUpdate.code_no} updated successfully`, "success");
    } catch (err) {
      showMessage(err.message, "error");
    } finally {
      setSaving(null);
    }
  };

  // Add new code
  const handleAddCode = async () => {
    if (!newCode.code_no || !newCode.description.trim()) {
      showMessage("Code number and description are required", "error");
      return;
    }

    if (!ALLOWED_CODES.includes(newCode.code_no)) {
      showMessage(`Only codes ${ALLOWED_CODES.join(', ')} are allowed`, "error");
      return;
    }

    if (newCode.default_amount && isNaN(Number(newCode.default_amount))) {
      showMessage("Amount must be a valid number", "error");
      return;
    }

    try {
      setSaving('new');
      const response = await fetch(`${process.env.REACT_APP_API_BASE}/codes`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          code_no: newCode.code_no,
          type: newCode.type,
          description: newCode.description.trim(),
          default_amount: newCode.default_amount ? Number(newCode.default_amount) : null
        })
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to add code');
      }

      const addedCode = await response.json();
      
      // Add to state
      setCodes(prevCodes => [...prevCodes, {
        ...addedCode,
        default_amount: addedCode.default_amount?.toString() || ""
      }]);

      // Reset form
      setNewCode({ code_no: "", type: "receipt", description: "", default_amount: "" });
      setShowAddForm(false);
      showMessage(`Code ${addedCode.code_no} added successfully`, "success");
    } catch (err) {
      showMessage(err.message, "error");
    } finally {
      setSaving(null);
    }
  };

  // Delete code
  const handleDelete = async (id) => {
    const codeToDelete = codes.find(code => code.id === id);
    if (!codeToDelete) return;

    if (!window.confirm(`Are you sure you want to delete code ${codeToDelete.code_no} - ${codeToDelete.description}?`)) {
      return;
    }

    try {
      setSaving(id);
      const response = await fetch(`${process.env.REACT_APP_API_BASE}/codes/${id}`, {
        method: 'DELETE'
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to delete code');
      }

      // Remove from state
      setCodes(prevCodes => prevCodes.filter(code => code.id !== id));
      showMessage(`Code ${codeToDelete.code_no} deleted successfully`, "success");
    } catch (err) {
      showMessage(err.message, "error");
    } finally {
      setSaving(null);
    }
  };

  // Helper function to show messages
  const showMessage = (message, type) => {
    if (type === "error") {
      setError(message);
      setTimeout(() => setError(""), 4000);
    } else {
      setSuccess(message);
      setTimeout(() => setSuccess(""), 3000);
    }
  };

  // Styles
  const containerStyle = {
    minHeight: "100vh",
    background: "linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%)",
    padding: "32px 0",
    fontFamily: "Inter, Arial, sans-serif"
  };

  const cardStyle = {
    maxWidth: 1200,
    margin: "0 auto",
    background: "#fff",
    borderRadius: 16,
    boxShadow: "0 6px 32px rgba(123,34,34,0.08)",
    padding: "32px"
  };

  const headerStyle = {
    textAlign: "center",
    marginBottom: "2rem"
  };

  const tableStyle = {
    width: "100%",
    borderCollapse: "collapse",
    background: "#fff",
    borderRadius: 12,
    overflow: "hidden",
    boxShadow: "0 2px 12px rgba(0,0,0,0.04)"
  };

  const thStyle = {
    background: "linear-gradient(90deg, #7b2222 70%, #a94442 100%)",
    color: "#fff",
    padding: "16px 20px",
    textAlign: "left",
    fontWeight: 700,
    fontSize: 15
  };

  const tdStyle = {
    padding: "16px 20px",
    borderBottom: "1px solid #f0f0f0",
    fontSize: 15,
    color: "#333"
  };

  const inputStyle = {
    width: "100%",
    padding: "8px 12px",
    border: "1px solid #ddd",
    borderRadius: 6,
    fontSize: 14,
    outline: "none"
  };

  const buttonStyle = {
    padding: "8px 16px",
    borderRadius: 6,
    border: "none",
    fontWeight: 600,
    fontSize: 13,
    cursor: "pointer",
    marginRight: "8px"
  };

  const saveButtonStyle = {
    ...buttonStyle,
    background: "#28a745",
    color: "white"
  };

  const deleteButtonStyle = {
    ...buttonStyle,
    background: "#dc3545",
    color: "white"
  };

  const addButtonStyle = {
    ...buttonStyle,
    background: "#007bff",
    color: "white",
    marginBottom: "20px"
  };

  if (loading) {
    return (
      <div style={containerStyle}>
        <div style={{ ...cardStyle, textAlign: "center", padding: "60px" }}>
          <div style={{ fontSize: "18px", color: "#666" }}>Loading codes...</div>
        </div>
      </div>
    );
  }

  return (
    <div style={containerStyle}>
      <div style={cardStyle}>
        <div style={headerStyle}>
          <h1 style={{ color: "#7b2222", fontWeight: 800, fontSize: "2rem", margin: 0 }}>
            Edit Codes
          </h1>
          <p style={{ color: "#666", fontSize: "1rem", margin: "10px 0 0 0" }}>
            Manage codes: R8, R13, P11, P19 only
          </p>
        </div>

        {/* Messages */}
        {error && (
          <div style={{
            background: "#f8d7da",
            color: "#721c24",
            padding: "12px 16px",
            borderRadius: "8px",
            marginBottom: "20px",
            border: "1px solid #f5c6cb"
          }}>
             {error}
          </div>
        )}

        {success && (
          <div style={{
            background: "#d4edda",
            color: "#155724",
            padding: "12px 16px",
            borderRadius: "8px",
            marginBottom: "20px",
            border: "1px solid #c3e6cb"
          }}>
             {success}
          </div>
        )}

        {/* Add New Code Button */}
        <button
          onClick={() => setShowAddForm(!showAddForm)}
          style={addButtonStyle}
        >
          {showAddForm ? "Cancel" : " Add New Code"}
        </button>

        {/* Add New Code Form */}
        {showAddForm && (
          <div style={{
            background: "#f8f9fa",
            padding: "20px",
            borderRadius: "8px",
            marginBottom: "20px",
            border: "1px solid #dee2e6"
          }}>
            <h3 style={{ margin: "0 0 15px 0", color: "#7b2222" }}>Add New Code</h3>
            <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 2fr 1fr auto", gap: "10px", alignItems: "end" }}>
              <div>
                <label style={{ display: "block", marginBottom: "5px", fontWeight: 600, fontSize: "13px" }}>
                  Code No:
                </label>
                <select
                  value={newCode.code_no}
                  onChange={(e) => handleNewCodeChange("code_no", e.target.value)}
                  style={inputStyle}
                >
                  <option value="">Select Code</option>
                  {ALLOWED_CODES.map(code => (
                    <option key={code} value={code}>{code}</option>
                  ))}
                </select>
              </div>
              <div>
                <label style={{ display: "block", marginBottom: "5px", fontWeight: 600, fontSize: "13px" }}>
                  Type:
                </label>
                <select
                  value={newCode.type}
                  onChange={(e) => handleNewCodeChange("type", e.target.value)}
                  style={inputStyle}
                >
                  <option value="receipt">Receipt</option>
                  <option value="payment">Payment</option>
                </select>
              </div>
              <div>
                <label style={{ display: "block", marginBottom: "5px", fontWeight: 600, fontSize: "13px" }}>
                  Description:
                </label>
                <input
                  type="text"
                  value={newCode.description}
                  onChange={(e) => handleNewCodeChange("description", e.target.value)}
                  style={inputStyle}
                  placeholder="Enter description"
                />
              </div>
              <div>
                <label style={{ display: "block", marginBottom: "5px", fontWeight: 600, fontSize: "13px" }}>
                  Amount:
                </label>
                <input
                  type="number"
                  value={newCode.default_amount}
                  onChange={(e) => handleNewCodeChange("default_amount", e.target.value)}
                  style={inputStyle}
                  min="0"
                  step="0.01"
                  placeholder="0.00"
                />
              </div>
              <button
                onClick={handleAddCode}
                disabled={saving === 'new'}
                style={{
                  ...saveButtonStyle,
                  opacity: saving === 'new' ? 0.6 : 1,
                  cursor: saving === 'new' ? "not-allowed" : "pointer"
                }}
              >
                {saving === 'new' ? "Adding..." : "Add"}
              </button>
            </div>
          </div>
        )}

        {/* Codes Table */}
        <table style={tableStyle}>
          <thead>
            <tr>
              <th style={thStyle}>Code No</th>
              <th style={thStyle}>Description</th>
              <th style={thStyle}>Default Amount ()</th>
              <th style={thStyle}>Actions</th>
            </tr>
          </thead>
          <tbody>
            {codes.length === 0 ? (
              <tr>
                <td colSpan="5" style={{ ...tdStyle, textAlign: "center", color: "#666", padding: "40px" }}>
                  No codes found. Add some codes using the button above.
                </td>
              </tr>
            ) : (
              codes.map(code => (
                <tr key={code.id}>
                  <td style={tdStyle}>
                    <span style={{ fontWeight: 600, color: "#7b2222" }}>{code.code_no}</span>
                    <span style={{ 
                      marginLeft: "8px", 
                      padding: "2px 8px", 
                      background: code.type === 'receipt' ? "#d4edda" : "#fff3cd",
                      color: code.type === 'receipt' ? "#155724" : "#856404",
                      borderRadius: "4px",
                      fontSize: "12px",
                      fontWeight: 600
                    }}>
                      {code.type}
                    </span>
                  </td>
                  
                  <td style={tdStyle}>
                    <input
                      type="text"
                      value={code.description || ""}
                      onChange={(e) => handleChange(code.id, "description", e.target.value)}
                      style={inputStyle}
                      placeholder="Enter description"
                    />
                  </td>
                  <td style={tdStyle}>
                    <input
                      type="number"
                      value={code.default_amount || ""}
                      onChange={(e) => handleChange(code.id, "default_amount", e.target.value)}
                      style={{ ...inputStyle, width: "120px", textAlign: "right" }}
                      min="0"
                      step="0.01"
                      placeholder="0.00"
                    />
                  </td>
                  <td style={tdStyle}>
                    <button
                      onClick={() => handleSave(code.id)}
                      disabled={saving === code.id}
                      style={{
                        ...saveButtonStyle,
                        opacity: saving === code.id ? 0.6 : 1,
                        cursor: saving === code.id ? "not-allowed" : "pointer"
                      }}
                    >
                      {saving === code.id ? "Saving..." : "Save"}
                    </button>
                    <button
                      onClick={() => handleDelete(code.id)}
                      disabled={saving === code.id}
                      style={{
                        ...deleteButtonStyle,
                        opacity: saving === code.id ? 0.6 : 1,
                        cursor: saving === code.id ? "not-allowed" : "pointer"
                      }}
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>

        {/* Footer Info */}
        <div style={{
          textAlign: "center",
          marginTop: "2rem",
          padding: "1rem",
          background: "#f8f9fa",
          borderRadius: "8px",
          fontSize: "13px",
          color: "#666"
        }}>
          <p style={{ margin: 0 }}>
            Only codes <strong>R8, R13, P11, P19</strong> can be managed here. 
            Changes sync with code_master table and are immediately available in forms.
          </p>
        </div>
      </div>
    </div>
  );
}
