import React, { useState } from "react";

export default function AddUser() {
  const [form, setForm] = useState({
    username: "",
    email: "",
    password: "",
    role: "admin",
  });
  const [loading, setLoading] = useState(false);
  const [successMsg, setSuccessMsg] = useState(null);
  const [errorMsg, setErrorMsg] = useState(null);
  const [showPassword, setShowPassword] = useState(false);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setSuccessMsg(null);
    setErrorMsg(null);

    try {
      const res = await fetch(`${process.env.REACT_APP_API_BASE}/auth/users`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });

      if (!res.ok) {
        const err = await res.json();
        throw new Error(err.error || "Failed to add user");
      }

      // UPDATE THIS SUCCESS MESSAGE
      setSuccessMsg("User added successfully and email sent with login credentials!");
      setForm({ username: "", email: "", password: "", role: "admin" });
    } catch (err) {
      setErrorMsg(err.message);
    } finally {
      setLoading(false);
      setTimeout(() => setSuccessMsg(null), 3000);
    }
  };


  const generatePassword = () => {
    const chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789@#$%";
    let password = "";
    for (let i = 0; i < 12; i++) {
      password += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    setForm({ ...form, password });
  };

  const formStyle = {
    maxWidth: 500,
    margin: "2.5rem auto",
    padding: "2.5rem 2.8rem 2rem 2.8rem",
    background: "linear-gradient(120deg, #fff 80%, #f8f5f2 100%)",
    borderRadius: 18,
    boxShadow: "0 6px 40px rgba(123,34,34,0.08)",
    fontFamily: "Inter, Arial, sans-serif"
  };

  const fieldStyle = { marginBottom: 20 };

  const labelStyle = {
    display: "block",
    marginBottom: 8,
    fontWeight: 600,
    color: "#7b2222",
    fontSize: 15,
    letterSpacing: 0.3
  };

  const inputStyle = {
    width: "100%",
    padding: "12px 16px",
    border: "1px solid #ddd",
    borderRadius: 8,
    fontSize: 16,
    background: "#faf9f7",
    transition: "all 0.2s ease",
    outline: "none",
    boxSizing: "border-box"
  };

  const selectStyle = {
    ...inputStyle,
    background: "#fff",
    cursor: "pointer"
  };

  const buttonStyle = {
    background: "linear-gradient(90deg, #7b2222 70%, #a94442 100%)",
    color: "#fff",
    border: "none",
    borderRadius: 10,
    padding: "12px 32px",
    fontWeight: 700,
    fontSize: 16,
    letterSpacing: 0.5,
    cursor: "pointer",
    boxShadow: "0 4px 16px rgba(123,34,34,0.15)",
    transition: "all 0.3s ease",
    width: "100%"
  };

  const secondaryButtonStyle = {
    background: "#f8f9fa",
    color: "#7b2222",
    border: "2px solid #7b2222",
    borderRadius: 8,
    padding: "8px 16px",
    fontWeight: 600,
    fontSize: 14,
    cursor: "pointer",
    transition: "all 0.2s ease",
    marginLeft: 8
  };

  return (
    <div style={{
      minHeight: "100vh",
      background: "linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%)",
      padding: "32px 0",
      fontFamily: "Inter, Arial, sans-serif"
    }}>
      <div style={formStyle}>
        {/* Header Section */}
        <div style={{ textAlign: "center", marginBottom: "2rem" }}>
          <div style={{
            width: "70px",
            height: "70px",
            background: "linear-gradient(135deg, #7b2222, #a94442)",
            borderRadius: "50%",
            margin: "0 auto 1rem auto",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            boxShadow: "0 6px 20px rgba(123,34,34,0.25)"
          }}>
            <div style={{
              fontSize: "28px",
              color: "#fff",
              fontWeight: "bold"
            }}>
              
            </div>
          </div>
          <h2 style={{
            color: "#7b2222",
            marginBottom: 8,
            fontWeight: 800,
            letterSpacing: 0.5,
            fontSize: "1.8rem"
          }}>
            Add New User
          </h2>
          <p style={{
            color: "#666",
            fontSize: "1rem",
            margin: 0,
            opacity: 0.8
          }}>
            Create a new user account for the system
          </p>
        </div>

        {/* Status Messages */}
        {successMsg && (
          <div style={{
            background: "linear-gradient(90deg, #d4edda, #c3e6cb)",
            color: "#155724",
            padding: "14px 18px",
            borderRadius: 10,
            marginBottom: 20,
            fontSize: 15,
            textAlign: "center",
            fontWeight: 600,
            border: "1px solid #c3e6cb",
            boxShadow: "0 2px 8px rgba(21,87,36,0.1)"
          }}>
             {successMsg}
          </div>
        )}

        {errorMsg && (
          <div style={{
            background: "linear-gradient(90deg, #f8d7da, #f5c6cb)",
            color: "#721c24",
            padding: "14px 18px",
            borderRadius: 10,
            marginBottom: 20,
            fontSize: 15,
            textAlign: "center",
            fontWeight: 600,
            border: "1px solid #f5c6cb",
            boxShadow: "0 2px 8px rgba(114,28,36,0.1)"
          }}>
             {errorMsg}
          </div>
        )}

        {/* Form */}
        <form onSubmit={handleSubmit}>
          <div style={fieldStyle}>
            <label style={labelStyle}>Username:</label>
            <input
              type="text"
              name="username"
              value={form.username}
              onChange={handleChange}
              required
              style={inputStyle}
              placeholder="Enter username"
              onFocus={(e) => {
                e.target.style.borderColor = "#7b2222";
                e.target.style.boxShadow = "0 0 0 3px rgba(123,34,34,0.1)";
              }}
              onBlur={(e) => {
                e.target.style.borderColor = "#ddd";
                e.target.style.boxShadow = "none";
              }}
            />
          </div>

          <div style={fieldStyle}>
            <label style={labelStyle}>Email:</label>
            <input
              type="email"
              name="email"
              value={form.email}
              onChange={handleChange}
              required
              style={inputStyle}
              placeholder="Enter email address"
              onFocus={(e) => {
                e.target.style.borderColor = "#7b2222";
                e.target.style.boxShadow = "0 0 0 3px rgba(123,34,34,0.1)";
              }}
              onBlur={(e) => {
                e.target.style.borderColor = "#ddd";
                e.target.style.boxShadow = "none";
              }}
            />
          </div>

          <div style={fieldStyle}>
            <label style={labelStyle}>Password:</label>
            <div style={{ position: "relative" }}>
              <input
                type={showPassword ? "text" : "password"}
                name="password"
                value={form.password}
                onChange={handleChange}
                required
                style={inputStyle}
                placeholder="Enter password"
                onFocus={(e) => {
                  e.target.style.borderColor = "#7b2222";
                  e.target.style.boxShadow = "0 0 0 3px rgba(123,34,34,0.1)";
                }}
                onBlur={(e) => {
                  e.target.style.borderColor = "#ddd";
                  e.target.style.boxShadow = "none";
                }}
              />
              <div style={{ display: "flex", gap: "8px", marginTop: "8px" }}>
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  style={{
                    ...secondaryButtonStyle,
                    marginLeft: 0,
                    flex: 1
                  }}
                >
                  {showPassword ? " Hide" : " Show"}
                </button>
                <button
                  type="button"
                  onClick={generatePassword}
                  style={{
                    ...secondaryButtonStyle,
                    flex: 1
                  }}
                >
                   Auto Generate Password
                </button>
              </div>
            </div>
          </div>

          <div style={fieldStyle}>
            <label style={labelStyle}>Role:</label>
            <select
              name="role"
              value={form.role}
              onChange={handleChange}
              required
              style={selectStyle}
            >
              <option value="admin">Admin</option>
              <option value="user">User</option>
            </select>
          </div>

          <button
            type="submit"
            disabled={loading}
            style={{
              ...buttonStyle,
              background: loading
                ? "linear-gradient(90deg, #999, #bbb)"
                : "linear-gradient(90deg, #7b2222 70%, #a94442 100%)",
              cursor: loading ? "not-allowed" : "pointer",
              transform: loading ? "none" : "translateY(0)",
            }}
            onMouseOver={(e) => {
              if (!loading) {
                e.target.style.transform = "translateY(-2px)";
                e.target.style.boxShadow = "0 6px 20px rgba(123,34,34,0.2)";
              }
            }}
            onMouseOut={(e) => {
              if (!loading) {
                e.target.style.transform = "translateY(0)";
                e.target.style.boxShadow = "0 4px 16px rgba(123,34,34,0.15)";
              }
            }}
          >
            {loading ? "Adding User..." : "Add User"}
          </button>
        </form>

        {/* Footer */}
        <div style={{
          textAlign: "center",
          marginTop: "2rem",
          paddingTop: "1.5rem",
          borderTop: "1px solid #eee"
        }}>
          <p style={{
            color: "#666",
            fontSize: "13px",
            margin: 0,
            opacity: 0.7
          }}>
            Users will receive login credentials via email
          </p>
        </div>
      </div>
    </div>
  );
}
