import React, { useState, useEffect } from 'react';

const ReceiptSearch = ({ selectedMonth, selectedYear, buildQueryParams }) => {
  const [searchType, setSearchType] = useState('receiptNo');
  const [searchValue, setSearchValue] = useState('');
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [pagination, setPagination] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);

  // Reset results when filters change
  useEffect(() => {
    if (results.length > 0) {
      setResults([]);
      setPagination(null);
      setCurrentPage(1);
    }
  }, [selectedMonth, selectedYear]);

  const handleSearch = async (page = 1) => {
    if (!searchValue.trim()) return;
    setLoading(true);
    setCurrentPage(page);

    try {
      let res;
      const dateFilters = buildQueryParams ? buildQueryParams() : '';

      if (searchType === "receiptNo") {
        // Single receipt search
        const searchParams = new URLSearchParams();
        if (dateFilters) {
          const urlParams = new URLSearchParams(dateFilters);
          urlParams.forEach((value, key) => {
            searchParams.append(key, value);
          });
        }

        res = await fetch(`${process.env.REACT_APP_API_BASE}/receipts/${searchValue}?${searchParams}`);
        if (res.ok) {
          const data = await res.json();
          if (data && (!dateFilters || isReceiptInDateRange(data))) {
            setResults([data]);
          } else {
            setResults([]);
          }
          setPagination(null);
        } else {
          setResults([]);
          setPagination(null);
        }
      } else if (searchType === "mobileNo") {
        // Mobile number search with pagination and date filters
        const searchParams = new URLSearchParams();
        searchParams.append('page', page.toString());
        searchParams.append('limit', '20');

        if (dateFilters) {
          const urlParams = new URLSearchParams(dateFilters);
          urlParams.forEach((value, key) => {
            searchParams.append(key, value);
          });
        }

        res = await fetch(`${process.env.REACT_APP_API_BASE}/receipts/mobile/${searchValue}?${searchParams}`);
        if (res.ok) {
          const data = await res.json();
          if (data.receipts && Array.isArray(data.receipts)) {
            setResults(data.receipts);
            setPagination(data.pagination);
          } else {
            setResults([]);
            setPagination(null);
          }
        } else {
          setResults([]);
          setPagination(null);
        }
      }
    } catch (error) {
      console.error("Search error:", error);
      setResults([]);
      setPagination(null);
    } finally {
      setLoading(false);
    }
  };

  // Helper function to check if single receipt is in date range
  const isReceiptInDateRange = (receipt) => {
    if (!selectedMonth && !selectedYear) return true;
    const receiptDate = new Date(receipt.transaction_date);
    const receiptMonth = receiptDate.getMonth() + 1;
    const receiptYear = receiptDate.getFullYear();
    const monthMatch = !selectedMonth || receiptMonth.toString() === selectedMonth;
    const yearMatch = !selectedYear || receiptYear.toString() === selectedYear;
    return monthMatch && yearMatch;
  };

  // Format seva dates for display
  const formatSevaDates = (sevaDates) => {
    if (!sevaDates || sevaDates.length === 0) return 'N/A';
    if (sevaDates.length === 1) {
      return new Date(sevaDates[0]).toLocaleDateString('en-IN');
    }
    if (sevaDates.length <= 3) {
      return sevaDates.map(date => new Date(date).toLocaleDateString('en-IN')).join(', ');
    }
    return `${new Date(sevaDates[0]).toLocaleDateString('en-IN')} + ${sevaDates.length - 1} more`;
  };

  // Get overall status from services
  const getOverallStatus = (services) => {
    if (!services || services.length === 0) return 'N/A';
    
    const statuses = services.map(s => s.status);
    const uniqueStatuses = [...new Set(statuses)];
    
    if (uniqueStatuses.length === 1) {
      return uniqueStatuses[0].charAt(0).toUpperCase() + uniqueStatuses[0].slice(1);
    }
    
    const completed = statuses.filter(s => s === 'completed').length;
    const total = statuses.length;
    return `${completed}/${total} Completed`;
  };

  // Handle pagination
  const handlePageChange = (newPage) => {
    if (searchType === 'mobileNo') {
      handleSearch(newPage);
    }
  };

  // Get filter display text
  const getFilterDisplayText = () => {
    if (!selectedMonth && !selectedYear) return "";
    const monthNames = ['', 'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 
                       'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    let filterText = "Filtered by: ";
    if (selectedMonth) filterText += monthNames[parseInt(selectedMonth)] + " ";
    if (selectedYear) filterText += selectedYear;
    return filterText;
  };

  // Styling
  const containerStyle = {
    background: "white",
    padding: "2rem",
    borderRadius: "16px",
    boxShadow: "0 8px 32px rgba(123, 34, 34, 0.1)",
    fontFamily: "Inter, Arial, sans-serif",
    border: "1px solid #f0f0f0"
  };

  const headerStyle = {
    textAlign: "center",
    marginBottom: "2rem",
    color: "#7b2222",
    fontSize: "1.5rem",
    fontWeight: "700"
  };

  const searchFormStyle = {
    background: "linear-gradient(135deg, #faf9f7 0%, #f5f3f1 100%)",
    padding: "1.5rem",
    borderRadius: "12px",
    marginBottom: "2rem",
    border: "1px solid #e9ecef"
  };

  const inputGroupStyle = {
    display: "flex",
    gap: "1rem",
    alignItems: "end",
    marginBottom: "1rem",
    flexWrap: "wrap"
  };

  const labelStyle = {
    display: "block",
    marginBottom: "0.5rem",
    fontWeight: "600",
    color: "#7b2222",
    fontSize: "14px"
  };

  const selectStyle = {
    padding: "10px 12px",
    border: "1px solid #ddd",
    borderRadius: "8px",
    fontSize: "14px",
    background: "white",
    fontWeight: "500",
    outline: "none",
    cursor: "pointer"
  };

  const inputStyle = {
    padding: "12px 16px",
    border: "1px solid #ddd",
    borderRadius: "8px",
    fontSize: "16px",
    minWidth: "220px",
    outline: "none",
    background: "#fafafa",
    transition: "all 0.2s ease",
    fontWeight: "500"
  };

  const buttonStyle = {
    padding: "12px 24px",
    background: loading ? "#999" : "linear-gradient(90deg, #7b2222 70%, #a94442 100%)",
    color: "white",
    border: "none",
    borderRadius: "8px",
    fontSize: "16px",
    fontWeight: "600",
    cursor: loading ? "not-allowed" : "pointer",
    transition: "all 0.3s ease",
    boxShadow: "0 4px 12px rgba(123, 34, 34, 0.2)"
  };

  const tableStyle = {
    width: "100%",
    borderCollapse: "collapse",
    background: "white",
    borderRadius: "12px",
    overflow: "hidden",
    boxShadow: "0 4px 16px rgba(0,0,0,0.08)",
    border: "1px solid #f0f0f0"
  };

  const thStyle = {
    background: "linear-gradient(90deg, #7b2222 70%, #a94442 100%)",
    color: "white",
    padding: "16px 12px",
    textAlign: "left",
    fontWeight: "600",
    fontSize: "13px",
    letterSpacing: "0.5px",
    textTransform: "uppercase"
  };

  const tdStyle = {
    padding: "16px 12px",
    borderBottom: "1px solid #f5f5f5",
    fontSize: "14px",
    color: "#333",
    verticalAlign: "top"
  };

  const statusStyle = (status) => ({
    padding: "4px 12px",
    borderRadius: "20px",
    fontSize: "12px",
    fontWeight: "600",
    textTransform: "uppercase",
    letterSpacing: "0.5px",
    background: status?.toLowerCase().includes('completed') ? '#e8f5e8' : 
                status?.toLowerCase().includes('scheduled') ? '#fff3cd' : '#f8d7da',
    color: status?.toLowerCase().includes('completed') ? '#28a745' : 
           status?.toLowerCase().includes('scheduled') ? '#856404' : '#721c24',
    border: `1px solid ${status?.toLowerCase().includes('completed') ? '#d4edda' : 
                          status?.toLowerCase().includes('scheduled') ? '#ffeaa7' : '#f5c6cb'}`
  });

  const paginationStyle = {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    gap: "0.5rem",
    marginTop: "2rem",
    padding: "1rem"
  };

  const paginationButtonStyle = (isActive = false, disabled = false) => ({
    padding: "8px 16px",
    border: isActive ? "2px solid #7b2222" : "1px solid #ddd",
    borderRadius: "8px",
    background: isActive ? "#7b2222" : "white",
    color: isActive ? "white" : disabled ? "#999" : "#333",
    cursor: disabled ? "not-allowed" : "pointer",
    fontSize: "14px",
    fontWeight: isActive ? "600" : "400",
    transition: "all 0.2s ease"
  });

  const noResultsStyle = {
    textAlign: "center",
    padding: "3rem 2rem",
    color: "#666",
    background: "#f9f9f9",
    borderRadius: "12px",
    border: "1px solid #e9ecef"
  };

  const filterTagStyle = {
    background: "#e8f4fd",
    color: "#0066cc",
    padding: "4px 12px",
    borderRadius: "20px",
    fontSize: "12px",
    fontWeight: "600",
    border: "1px solid #b8daff"
  };

  return (
    <div style={containerStyle}>
      <h3 style={headerStyle}> Receipt Search</h3>
      
      <div style={searchFormStyle}>
        {getFilterDisplayText() && (
          <div style={{ marginBottom: "1rem", textAlign: "center" }}>
            <span style={filterTagStyle}>{getFilterDisplayText()}</span>
          </div>
        )}
        
        <div style={inputGroupStyle}>
          <div>
            <label style={labelStyle}>Search by:</label>
            <select
              style={selectStyle}
              value={searchType}
              onChange={(e) => setSearchType(e.target.value)}
            >
              <option value="receiptNo">Receipt Number</option>
              <option value="mobileNo">Mobile Number</option>
            </select>
          </div>
          
          <div>
            <label style={labelStyle}>
              {searchType === 'receiptNo' ? 'Receipt Number' : 'Mobile Number'}:
            </label>
            <input
              type="text"
              style={inputStyle}
              value={searchValue}
              onChange={(e) => setSearchValue(e.target.value)}
              placeholder={searchType === 'receiptNo' ? 'Enter receipt number...' : 'Enter 10-digit mobile number...'}
              onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
            />
          </div>
          
          <button
            style={buttonStyle}
            onClick={() => handleSearch()}
            disabled={loading || !searchValue.trim()}
          >
            {loading ? 'Searching...' : ' Search'}
          </button>
        </div>
      </div>

      {results.length > 0 && (
        <div>
          <table style={tableStyle}>
            <thead>
              <tr>
                <th style={thStyle}>Receipt No</th>
                <th style={thStyle}>Mobile No</th>
                <th style={thStyle}>Code</th>
                <th style={thStyle}>Description</th>
                <th style={thStyle}>Transaction Date</th>
                <th style={thStyle}>Seva Dates</th>
                <th style={thStyle}>Status</th>
              </tr>
            </thead>
            <tbody>
              {results.map((r, index) => (
                <tr key={r.receipt_no} style={{ background: index % 2 === 0 ? '#fafafa' : 'white' }}>
                  <td style={{...tdStyle, fontWeight: '600', color: '#7b2222'}}>{r.receipt_no}</td>
                  <td style={tdStyle}>{r.mobile_no}</td>
                  <td style={{...tdStyle, fontWeight: '600'}}>{r.code_no}</td>
                  <td style={tdStyle}>{r.description}</td>
                  <td style={tdStyle}>
                    {r.transaction_date ? new Date(r.transaction_date).toLocaleDateString('en-IN') : 'N/A'}
                  </td>
                  <td style={tdStyle}>
                    <div title={r.seva_dates ? r.seva_dates.map(d => new Date(d).toLocaleDateString('en-IN')).join(', ') : 'N/A'}>
                      {formatSevaDates(r.seva_dates)}
                    </div>
                  </td>
                  <td style={tdStyle}>
                    <span style={statusStyle(getOverallStatus(r.services))}>
                      {getOverallStatus(r.services)}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>

          {/* Pagination for mobile search */}
          {pagination && pagination.totalPages > 1 && (
            <div style={paginationStyle}>
              <button
                style={paginationButtonStyle(false, !pagination.hasPrev)}
                onClick={() => handlePageChange(currentPage - 1)}
                disabled={!pagination.hasPrev}
              >
                 Previous
              </button>
              
              <span style={{ margin: "0 1rem", fontSize: "14px", color: "#666" }}>
                Page {pagination.currentPage} of {pagination.totalPages} 
                ({pagination.totalRecords} total)
              </span>
              
              <button
                style={paginationButtonStyle(false, !pagination.hasNext)}
                onClick={() => handlePageChange(currentPage + 1)}
                disabled={!pagination.hasNext}
              >
                Next 
              </button>
            </div>
          )}
        </div>
      )}

      {!loading && results.length === 0 && searchValue.trim() && (
        <div style={noResultsStyle}>
          <h4 style={{ color: "#7b2222", marginBottom: "1rem" }}>No receipts found</h4>
          <p style={{ margin: 0, fontSize: "14px" }}>
            No receipts found for {searchType === 'receiptNo' ? 'receipt number' : 'mobile number'}: 
            <strong> {searchValue}</strong>
          </p>
          {getFilterDisplayText() && (
            <p style={{ margin: "0.5rem 0 0 0", fontSize: "12px", fontStyle: "italic" }}>
              ({getFilterDisplayText()})
            </p>
          )}
        </div>
      )}
    </div>
  );
};

export default ReceiptSearch;
