import React, { useEffect, useState } from "react";

export default function EditRecentJournals() {
  const [journals, setJournals] = useState([]);
  const [loading, setLoading] = useState(true);
  const [savingId, setSavingId] = useState(null);
  const [error, setError] = useState(null);
  const [successMsg, setSuccessMsg] = useState(null);

  useEffect(() => {
    fetch(`${process.env.REACT_APP_API_BASE}/journals/latest`)
      .then(res => {
        if (!res.ok) throw new Error("Failed to fetch journals");
        return res.json();
      })
      .then(data => {
        setJournals(data);
        setLoading(false);
      })
      .catch(err => {
        setError("Failed to load journal entries");
        setLoading(false);
      });
  }, []);

  const handleChange = (id, field, value) => {
    setJournals(journals => 
      journals.map(j => 
        (j.journal_no === id ? { ...j, [field]: value } : j)
      )
    );
  };

  const validateForm = (journal) => {
    if (!journal.amount || journal.amount <= 0) {
      return "Amount must be greater than 0";
    }
    if (!journal.description?.trim()) {
      return "Description is required";
    }
    if (!journal.transaction_date) {
      return "Transaction date is required";
    }
    if (!journal.transaction_type) {
      return "Transaction type is required";
    }
    if (!journal.mode_of_payment) {
      return "Payment mode is required";
    }
    if (journal.mode_of_payment === "cheque" && !journal.cheque_no?.trim()) {
      return "Cheque number is required for cheque payments";
    }
    return null;
  };

  const handleSave = async (id) => {
    const journal = journals.find(j => j.journal_no === id);
    if (!journal) return;

    const validationError = validateForm(journal);
    if (validationError) {
      setError(validationError);
      return;
    }

    setSavingId(id);
    setError(null);
    setSuccessMsg(null);

    try {
      const journalData = {
        amount: Number(journal.amount),
        transaction_date: journal.transaction_date,
        mode_of_payment: journal.mode_of_payment
      };

      if (journal.mode_of_payment === "cheque") {
        journalData.cheque_no = journal.cheque_no;
        journalData.cheque_date = journal.cheque_date;
      }

      const res = await fetch(`${process.env.REACT_APP_API_BASE}/journals/${id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(journalData),
      });

      if (!res.ok) {
        const errData = await res.json();
        throw new Error(errData.error || "Failed to update journal");
      }

      setSuccessMsg(`Journal ${id} updated successfully`);
    } catch (err) {
      setError(err.message);
    } finally {
      setSavingId(null);
      setTimeout(() => setSuccessMsg(null), 3000);
    }
  };

  // Styles (keeping consistent with other edit components)
  const containerStyle = {
    minHeight: "100vh",
    background: "linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%)",
    padding: "32px 0",
    fontFamily: "Inter, Arial, sans-serif"
  };

  const cardStyle = {
    maxWidth: 1400,
    margin: "0 auto",
    background: "#fff",
    borderRadius: 16,
    boxShadow: "0 6px 32px rgba(123,34,34,0.08)",
    padding: "32px 32px 24px 32px"
  };

  const headerStyle = {
    textAlign: "center",
    marginBottom: "2rem"
  };

  const titleStyle = {
    color: "#7b2222",
    fontWeight: 800,
    marginBottom: 8,
    fontSize: "2rem",
    letterSpacing: 0.5
  };

  const subtitleStyle = {
    color: "#666",
    fontSize: "1rem",
    margin: 0,
    opacity: 0.8
  };

  const tableStyle = {
    width: "100%",
    borderCollapse: "collapse",
    background: "#fff",
    borderRadius: 12,
    overflow: "hidden",
    boxShadow: "0 2px 12px rgba(0,0,0,0.04)"
  };

  const thStyle = {
    background: "linear-gradient(90deg, #7b2222 70%, #a94442 100%)",
    color: "#fff",
    padding: "14px 12px",
    textAlign: "left",
    fontWeight: 700,
    fontSize: 14,
    letterSpacing: 0.3
  };

  const tdStyle = {
    padding: "12px",
    borderBottom: "1px solid #f0f0f0",
    fontSize: 14,
    color: "#333",
    verticalAlign: "top"
  };

  const inputStyle = {
    width: "100%",
    padding: "8px 10px",
    border: "1px solid #ddd",
    borderRadius: 6,
    fontSize: 14,
    background: "#faf9f7",
    transition: "all 0.2s ease",
    outline: "none"
  };

  const selectStyle = {
    ...inputStyle,
    background: "#fff",
    cursor: "pointer"
  };

  const buttonStyle = {
    background: "linear-gradient(90deg, #7b2222 70%, #a94442 100%)",
    color: "#fff",
    border: "none",
    borderRadius: 6,
    padding: "8px 16px",
    fontWeight: 600,
    fontSize: 13,
    cursor: "pointer",
    transition: "all 0.3s ease",
    boxShadow: "0 2px 8px rgba(123,34,34,0.15)"
  };

  const loadingStyle = {
    textAlign: "center",
    padding: "60px 20px",
    fontSize: "1.1rem",
    color: "#666"
  };

  if (loading) {
    return (
      <div style={containerStyle}>
        <div style={cardStyle}>
          <div style={loadingStyle}>
             Loading recent journals...
          </div>
        </div>
      </div>
    );
  }

  return (
    <div style={containerStyle}>
      <div style={cardStyle}>
        <div style={headerStyle}>
          <h1 style={titleStyle}> Edit Recent Journals</h1>
          <p style={subtitleStyle}>
            Modify the most recent 10 journal entries (Cash/Bank transfers)
          </p>
        </div>

        {error && (
          <div style={{
            background: "linear-gradient(90deg, #f8d7da, #f5c6cb)",
            color: "#721c24",
            padding: "14px 18px",
            borderRadius: 10,
            marginBottom: 20,
            fontSize: 15,
            fontWeight: 600,
            border: "1px solid #f5c6cb"
          }}>
             {error}
          </div>
        )}

        {successMsg && (
          <div style={{
            background: "linear-gradient(90deg, #d4edda, #c3e6cb)",
            color: "#155724",
            padding: "14px 18px",
            borderRadius: 10,
            marginBottom: 20,
            fontSize: 15,
            fontWeight: 600,
            border: "1px solid #c3e6cb"
          }}>
             {successMsg}
          </div>
        )}

        <table style={tableStyle}>
          <thead>
            <tr>
              <th style={thStyle}>Journal No</th>
              <th style={thStyle}>Transaction Type</th>
              <th style={thStyle}>Description</th>
              <th style={thStyle}>Amount ()</th>
              <th style={thStyle}>Transaction Date</th>
              <th style={thStyle}>Payment Mode</th>
              <th style={thStyle}>Cheque No</th>
              <th style={thStyle}>Cheque Date</th>
              <th style={thStyle}>Actions</th>
            </tr>
          </thead>
          <tbody>
            {journals.length === 0 ? (
              <tr>
                <td colSpan="9" style={{ ...tdStyle, textAlign: "center", padding: "40px", color: "#666" }}>
                  No recent journal entries found
                </td>
              </tr>
            ) : (
              journals.map((journal) => (
                <tr key={journal.journal_no}>
                  <td style={tdStyle}>
                    <strong>{journal.journal_no}</strong>
                  </td>
                  <td style={tdStyle}>
                    <span style={{ 
                      padding: "4px 8px",
                      borderRadius: "4px",
                      fontSize: "12px",
                      fontWeight: "600",
                      background: journal.transaction_type === 'cashDrawn' ? "#e8f5e8" : "#fff3cd",
                      color: journal.transaction_type === 'cashDrawn' ? "#155724" : "#856404"
                    }}>
                      {journal.transaction_type === 'cashDrawn' ? 'Cash Drawn' : 'Cash Deposit'}
                    </span>
                  </td>
                  <td style={tdStyle}>
                    <span style={{ color: "#666", fontStyle: "italic" }}>
                      {journal.description}
                    </span>
                  </td>
                  <td style={tdStyle}>
                    <input
                      type="number"
                      value={journal.amount || ""}
                      onChange={(e) => handleChange(journal.journal_no, "amount", e.target.value)}
                      style={inputStyle}
                      min="0"
                      step="0.01"
                      placeholder="0.00"
                    />
                  </td>
                  <td style={tdStyle}>
                    <input
                      type="date"
                      value={journal.transaction_date || ""}
                      onChange={(e) => handleChange(journal.journal_no, "transaction_date", e.target.value)}
                      style={inputStyle}
                    />
                  </td>
                  <td style={tdStyle}>
                    <select
                      value={journal.mode_of_payment || "cash"}
                      onChange={(e) => handleChange(journal.journal_no, "mode_of_payment", e.target.value)}
                      style={selectStyle}
                    >
                      <option value="cash">Cash</option>
                      <option value="cheque">Cheque</option>
                      <option value="upi">UPI</option>
                      <option value="online">Online</option>
                      <option value="bank">Bank</option>
                    </select>
                  </td>
                  <td style={tdStyle}>
                    <input
                      type="text"
                      value={journal.cheque_no || ""}
                      onChange={(e) => handleChange(journal.journal_no, "cheque_no", e.target.value)}
                      style={inputStyle}
                      placeholder="Cheque number"
                      disabled={journal.mode_of_payment !== "cheque"}
                    />
                  </td>
                  <td style={tdStyle}>
                    <input
                      type="date"
                      value={journal.cheque_date || ""}
                      onChange={(e) => handleChange(journal.journal_no, "cheque_date", e.target.value)}
                      style={inputStyle}
                      disabled={journal.mode_of_payment !== "cheque"}
                    />
                  </td>
                  <td style={tdStyle}>
                    <button
                      onClick={() => handleSave(journal.journal_no)}
                      disabled={savingId === journal.journal_no}
                      style={{
                        ...buttonStyle,
                        opacity: savingId === journal.journal_no ? 0.6 : 1,
                        cursor: savingId === journal.journal_no ? "not-allowed" : "pointer"
                      }}
                    >
                      {savingId === journal.journal_no ? "Saving..." : "Save"}
                    </button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>

        <div style={{
          marginTop: "2rem",
          padding: "1rem",
          background: "#f8f9fa",
          borderRadius: "8px",
          fontSize: "14px",
          color: "#666",
          textAlign: "center"
        }}>
          Only the most recent 10 journal entries are shown. Changes are saved individually.
        </div>
      </div>
    </div>
  );
}
