import React, { useState } from "react";
import EditRecentReceipts from "./EditRecentReceipts";
import EditRecentPayments from "./EditRecentPayments";
import EditRecentJournals from "./EditRecentJournals";

export default function Transaction() {
  const [activeTab, setActiveTab] = useState("receipts");

  const containerStyle = {
    minHeight: "100vh",
    background: "linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%)",
    padding: "32px 0",
    fontFamily: "Inter, Arial, sans-serif"
  };

  const headerStyle = {
    textAlign: "center",
    marginBottom: "2rem"
  };

  const titleStyle = {
    color: "#7b2222",
    fontWeight: 800,
    marginBottom: 8,
    fontSize: "2.5rem",
    letterSpacing: 0.5
  };

  const subtitleStyle = {
    color: "#666",
    fontSize: "1.1rem",
    margin: 0,
    opacity: 0.8
  };

  const tabContainerStyle = {
    display: "flex",
    justifyContent: "center",
    marginBottom: "2rem",
    background: "#fff",
    borderRadius: "12px",
    padding: "8px",
    boxShadow: "0 4px 20px rgba(123,34,34,0.08)",
    maxWidth: "600px",
    margin: "0 auto 2rem auto"
  };

  const getTabStyle = (tabKey) => ({
    padding: "12px 24px",
    borderRadius: "8px",
    border: "none",
    fontSize: "16px",
    fontWeight: 600,
    cursor: "pointer",
    transition: "all 0.3s ease",
    background: activeTab === tabKey 
      ? "linear-gradient(90deg, #7b2222 70%, #a94442 100%)" 
      : "transparent",
    color: activeTab === tabKey ? "#fff" : "#7b2222",
    flex: 1,
    margin: "0 4px",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    gap: "8px"
  });

  const renderContent = () => {
    switch (activeTab) {
      case "receipts":
        return <EditRecentReceipts />;
      case "payments":
        return <EditRecentPayments />;
      case "journals":
        return <EditRecentJournals />;
      default:
        return <EditRecentReceipts />;
    }
  };

  return (
    <div>
      {/* Header Section */}
      <div style={headerStyle}>
        <h1 style={titleStyle}> Transaction Management</h1>
        <p style={subtitleStyle}>
          Edit recent receipts, payments, and journal entries
        </p>
      </div>

      {/* Tab Navigation */}
      <div style={tabContainerStyle}>
        <button
          onClick={() => setActiveTab("receipts")}
          style={getTabStyle("receipts")}
          onMouseOver={(e) => {
            if (activeTab !== "receipts") {
              e.target.style.background = "#f8f9fa";
            }
          }}
          onMouseOut={(e) => {
            if (activeTab !== "receipts") {
              e.target.style.background = "transparent";
            }
          }}
        >
           Receipts
        </button>
        
        <button
          onClick={() => setActiveTab("payments")}
          style={getTabStyle("payments")}
          onMouseOver={(e) => {
            if (activeTab !== "payments") {
              e.target.style.background = "#f8f9fa";
            }
          }}
          onMouseOut={(e) => {
            if (activeTab !== "payments") {
              e.target.style.background = "transparent";
            }
          }}
        >
           Payments
        </button>
        
        <button
          onClick={() => setActiveTab("journals")}
          style={getTabStyle("journals")}
          onMouseOver={(e) => {
            if (activeTab !== "journals") {
              e.target.style.background = "#f8f9fa";
            }
          }}
          onMouseOut={(e) => {
            if (activeTab !== "journals") {
              e.target.style.background = "transparent";
            }
          }}
        >
           Journals
        </button>
      </div>

      {/* Content Area */}
      <div>
        {renderContent()}
      </div>
    </div>
  );
}
