import React from "react";

import AppLayout from "./AppLayout";
import LoginPage from "./pages/LoginPage";
import Unauthorized from "./pages/Unauthorized";
import HomePage from "./pages/HomePage";
import ProtectedRoute from "./components/ProtectedRoute";
import ReceiptForm from "./components/ReceiptsForm";
import PaymentForm from "./components/PaymentsForm";
import JournalForm from "./components/JournalForm";
import CombinedReports from "./components/CombinedReports";
import AddUser from "./components/AddUser";
import CodeMasterAdmin from "./components/CodeMasterAdmin";
import EditRecentReceipts from "./components/EditRecentReceipts";
import EditRecentPayments from "./components/EditRecentPayments";
import LogoutPage from "./components/Logout";
import { Navigate } from "react-router-dom";
import Transaction from "./components/Transaction";
import { BrowserRouter, HashRouter, Routes, Route } from "react-router-dom";



function App() {
  return (
    <BrowserRouter>
      <Routes>
        {/* Login and Unauthorized pages: no header/footer */}
        <Route path="/login" element={<LoginPage />} />
        <Route path="/unauthorized" element={<Unauthorized />} />
        <Route path="/logout" element={<LogoutPage />} />

        {/* All other routes: wrapped in layout */}
        <Route
          path="*"
          element={
            <AppLayout>
              <Routes>
                <Route path="/dashboard" element={
                  <ProtectedRoute allowedRoles={["user", "admin"]}>
                    <HomePage />
                  </ProtectedRoute>
                } />
                <Route path="/receipt" element={
                  <ProtectedRoute allowedRoles={["user", "admin"]}>
                    <ReceiptForm />
                  </ProtectedRoute>
                } />
                <Route path="/payment" element={
                  <ProtectedRoute allowedRoles={["user", "admin"]}>
                    <PaymentForm />
                  </ProtectedRoute>
                } />
                <Route path="/journal" element={
                  <ProtectedRoute allowedRoles={["user", "admin"]}>
                    <JournalForm />
                  </ProtectedRoute>
                } />
                <Route path="/report" element={
                  <ProtectedRoute allowedRoles={["user", "admin"]}>
                    <CombinedReports />
                  </ProtectedRoute>
                } />
                <Route path="/addUser" element={
                  <ProtectedRoute allowedRoles={["admin"]}>
                    <AddUser />
                  </ProtectedRoute>
                } />
                <Route path="/edit" element={
                  <ProtectedRoute allowedRoles={["admin"]}>
                    <CodeMasterAdmin />
                  </ProtectedRoute>
                } />
                <Route path="/editreceipt" element={
                  <ProtectedRoute allowedRoles={["admin"]}>
                    <EditRecentReceipts />
                  </ProtectedRoute>
                } />
                <Route path="/editpayment" element={
                  <ProtectedRoute allowedRoles={["admin"]}>
                    <EditRecentPayments />
                  </ProtectedRoute>
                } />
                <Route path="/edittransaction" element={
                  <ProtectedRoute allowedRoles={["admin"]}>
                    <Transaction />
                  </ProtectedRoute>
                } />
                <Route path="*" element={
                  <Navigate to="/logout" replace />
                } />
                {/* Add more protected routes here */}
              </Routes>
            </AppLayout>
          }
        />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
