import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import MenuIcon from '@mui/icons-material/Menu'; // Added for hamburger menu

const Header = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);
  const navigate = useNavigate();

  // Get user role from localStorage
  const userRole = localStorage.getItem("userRole");

  // Handle sign out
  const handleSignOut = () => {
    localStorage.removeItem("token");
    localStorage.removeItem("userRole");
    localStorage.removeItem("username");
    navigate("/login");
  };

  // Toggle profile dropdown
  const toggleMenu = () => setMenuOpen((open) => !open);

  // Toggle mobile nav menu
  const toggleMobileMenu = () => setMobileMenuOpen((open) => !open);

  // Close dropdown when clicking outside
  const handleBlur = (e) => {
    if (!e.currentTarget.contains(e.relatedTarget)) {
      setMenuOpen(false);
    }
  };

  // Detect screen size changes
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768);
      if (window.innerWidth >= 768) setMobileMenuOpen(false); // Close mobile menu on larger screens
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Base styles (adjusted for responsiveness)
  const headerStyle = {
    background: 'linear-gradient(135deg, #7b2222 0%, #a94442 100%)',
    color: '#fff',
    padding: '0',
    boxShadow: '0 4px 20px rgba(123, 34, 34, 0.3)',
    position: 'sticky',
    top: 0,
    zIndex: 1000,
    minHeight: isMobile ? '60px' : '85px',
  };

  const containerStyle = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    width: '100%',
    maxWidth: '1400px',
    margin: '0 auto',
    padding: isMobile ? '0.5rem 0.8rem' : '0.8rem 1rem',
    gap: isMobile ? '1rem' : '2rem',
    flexWrap: 'wrap', // Allow wrapping on small screens
  };

  const logoTitleStyle = {
    display: 'flex',
    alignItems: 'center',
    flex: '0 0 auto',
    minWidth: 'fit-content',
  };

  const navStyle = {
    display: isMobile ? (mobileMenuOpen ? 'flex' : 'none') : 'flex',
    flexDirection: isMobile ? 'column' : 'row',
    gap: isMobile ? '1rem' : '1.5rem',
    flex: '1 1 auto',
    justifyContent: 'center',
    alignItems: 'center',
    background: isMobile ? '#fff' : 'transparent',
    position: isMobile ? 'absolute' : 'static',
    top: isMobile ? '100%' : 'auto',
    left: 0,
    width: isMobile ? '100%' : 'auto',
    padding: isMobile ? '1rem' : '0',
    boxShadow: isMobile ? '0 4px 12px rgba(0,0,0,0.1)' : 'none',
    zIndex: 999,
  };

  const navLinkStyle = {
    color: isMobile ? '#333' : '#fff',
    textDecoration: 'none',
    fontSize: isMobile ? '1rem' : '1.1rem',
    fontWeight: 500,
    padding: isMobile ? '0.8rem 1rem' : '0.6rem 1rem',
    borderRadius: '8px',
    transition: 'all 0.3s ease',
    position: 'relative',
    width: isMobile ? '100%' : 'auto',
    textAlign: isMobile ? 'left' : 'center',
    background: isMobile ? '#f8f9fa' : 'transparent',
  };

  const menuItemStyle = {
    width: "100%",
    padding: "12px 18px",
    border: "none",
    background: "none",
    textAlign: "left",
    cursor: "pointer",
    fontSize: 16,
    color: "#333",
    borderRadius: 4,
    transition: "background 0.2s"
  };

  return (
    <header style={headerStyle}>
      <div style={containerStyle}>
        {/* Left: Logo and Title */}
        <div style={logoTitleStyle}>
          <div style={{ position: 'relative', marginRight: isMobile ? '0.8rem' : '1.2rem' }}>
            <img
              src="/icon.png"
              alt="Sringeri Logo"
              style={{
                height: isMobile ? '50px' : '68px',
                width: isMobile ? '50px' : '68px',
                background: 'linear-gradient(145deg, #ffffff, #f0f0f0)',
                borderRadius: '50%',
                border: '3px solid rgba(255, 255, 255, 0.9)',
                boxShadow: '0 6px 20px rgba(0,0,0,0.15), inset 0 1px 3px rgba(255,255,255,0.3)',
                transition: 'transform 0.3s ease, box-shadow 0.3s ease',
                cursor: 'pointer'
              }}
              onMouseOver={(e) => {
                e.target.style.transform = 'scale(1.05)';
                e.target.style.boxShadow = '0 8px 25px rgba(0,0,0,0.2), inset 0 1px 3px rgba(255,255,255,0.4)';
              }}
              onMouseOut={(e) => {
                e.target.style.transform = 'scale(1)';
                e.target.style.boxShadow = '0 6px 20px rgba(0,0,0,0.15), inset 0 1px 3px rgba(255,255,255,0.3)';
              }}
            />
            <div style={{
              position: 'absolute',
              top: '-4px',
              left: '-4px',
              right: '-4px',
              bottom: '-4px',
              borderRadius: '50%',
              background: 'linear-gradient(45deg, rgba(255,224,130,0.3), rgba(255,255,255,0.2))',
              zIndex: -1
            }} />
          </div>
          <div style={{ textAlign: 'left' }}>
            <h1 style={{
              fontFamily: 'Georgia, serif',
              fontSize: isMobile ? '1.2rem' : '1.6rem',
              margin: 0,
              fontWeight: 700,
              letterSpacing: '0.8px',
              textShadow: '2px 2px 4px rgba(0,0,0,0.3)',
              background: 'linear-gradient(45deg, #fff, #ffe082)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              backgroundClip: 'text'
            }}>
              Sri Sringeri Shankar Math
            </h1>
            <div style={{
              fontSize: isMobile ? '0.8rem' : '0.95rem',
              marginTop: '0.2rem',
              fontWeight: 400,
              letterSpacing: '0.3px',
              opacity: 0.9,
              fontStyle: 'italic'
            }}>
              Motinagar, Hyderabad
            </div>
          </div>
        </div>

        {/* Hamburger Icon for Mobile */}
        {isMobile && (
          <button
            onClick={toggleMobileMenu}
            style={{
              background: 'transparent',
              border: 'none',
              cursor: 'pointer',
              color: '#fff',
              fontSize: '1.5rem',
            }}
          >
            <MenuIcon fontSize="large" />
          </button>
        )}

        {/* Navigation Options */}
        <nav style={navStyle}>
          <Link
            to="/dashboard"
            style={navLinkStyle}
            onMouseOver={(e) => {
              e.target.style.background = isMobile ? '#e9ecef' : 'rgba(255,255,255,0.15)';
              e.target.style.transform = 'translateY(-2px)';
            }}
            onMouseOut={(e) => {
              e.target.style.background = isMobile ? '#f8f9fa' : 'transparent';
              e.target.style.transform = 'translateY(0)';
            }}
            onClick={() => setMobileMenuOpen(false)}
          >
            Home
          </Link>
          <Link
            to="/receipt"
            style={navLinkStyle}
            onMouseOver={(e) => {
              e.target.style.background = isMobile ? '#e9ecef' : 'rgba(255,255,255,0.15)';
              e.target.style.transform = 'translateY(-2px)';
            }}
            onMouseOut={(e) => {
              e.target.style.background = isMobile ? '#f8f9fa' : 'transparent';
              e.target.style.transform = 'translateY(0)';
            }}
            onClick={() => setMobileMenuOpen(false)}
          >
            Receipt
          </Link>
          <Link
            to="/payment"
            style={navLinkStyle}
            onMouseOver={(e) => {
              e.target.style.background = isMobile ? '#e9ecef' : 'rgba(255,255,255,0.15)';
              e.target.style.transform = 'translateY(-2px)';
            }}
            onMouseOut={(e) => {
              e.target.style.background = isMobile ? '#f8f9fa' : 'transparent';
              e.target.style.transform = 'translateY(0)';
            }}
            onClick={() => setMobileMenuOpen(false)}
          >
            Payment
          </Link>
          <Link
            to="/journal"
            style={navLinkStyle}
            onMouseOver={(e) => {
              e.target.style.background = isMobile ? '#e9ecef' : 'rgba(255,255,255,0.15)';
              e.target.style.transform = 'translateY(-2px)';
            }}
            onMouseOut={(e) => {
              e.target.style.background = isMobile ? '#f8f9fa' : 'transparent';
              e.target.style.transform = 'translateY(0)';
            }}
            onClick={() => setMobileMenuOpen(false)}
          >
            Journal
          </Link>
          <Link
            to="/report"
            style={navLinkStyle}
            onMouseOver={(e) => {
              e.target.style.background = isMobile ? '#e9ecef' : 'rgba(255,255,255,0.15)';
              e.target.style.transform = 'translateY(-2px)';
            }}
            onMouseOut={(e) => {
              e.target.style.background = isMobile ? '#f8f9fa' : 'transparent';
              e.target.style.transform = 'translateY(0)';
            }}
            onClick={() => setMobileMenuOpen(false)}
          >
            Report
          </Link>
        </nav>

        {/* Right: Profile Icon & Dropdown */}
        <div style={{ position: "relative", flex: '0 0 auto' }} tabIndex={0} onBlur={handleBlur}>
          <button
            onClick={toggleMenu}
            style={{
              background: "rgba(255,255,255,0.1)",
              border: "2px solid rgba(255,255,255,0.3)",
              borderRadius: "50%",
              cursor: "pointer",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              padding: isMobile ? "6px" : "8px",
              transition: "all 0.3s ease",
              backdropFilter: "blur(10px)"
            }}
            onMouseOver={(e) => {
              e.target.style.background = "rgba(255,255,255,0.2)";
              e.target.style.transform = "scale(1.1)";
            }}
            onMouseOut={(e) => {
              e.target.style.background = "rgba(255,255,255,0.1)";
              e.target.style.transform = "scale(1)";
            }}
            aria-label="Open profile menu"
          >
            <AccountCircleIcon fontSize={isMobile ? "medium" : "large"} style={{ color: '#fff' }} />
          </button>
          {menuOpen && (
            <div
              style={{
                position: "absolute",
                right: 0,
                top: "100%",
                background: "#fff",
                boxShadow: "0 8px 32px rgba(0,0,0,0.15)",
                borderRadius: 12,
                marginTop: 8,
                minWidth: isMobile ? 180 : 200,
                zIndex: 1000,
                color: "#333",
                overflow: "hidden",
                border: "1px solid rgba(0,0,0,0.1)"
              }}
            >
              {/* Admin-only section */}
              {userRole === "admin" && (
                <div>
                  <div style={{
                    fontWeight: 700,
                    fontSize: isMobile ? 13 : 14,
                    color: "#7b2222",
                    padding: "10px 18px 4px 18px",
                    letterSpacing: 0.5,
                    textTransform: "uppercase"
                  }}>Admin</div>
                  <button
                    onClick={() => { setMenuOpen(false); navigate('/addUser'); }}
                    style={menuItemStyle}
                    onMouseOver={(e) => e.target.style.background = "#f8f9fa"}
                    onMouseOut={(e) => e.target.style.background = "none"}
                  >
                    Add User
                  </button>
                  <button
                    onClick={() => { setMenuOpen(false); navigate('/edit'); }}
                    style={menuItemStyle}
                    onMouseOver={(e) => e.target.style.background = "#f8f9fa"}
                    onMouseOut={(e) => e.target.style.background = "none"}
                  >
                    Edit Codes
                  </button>
                  <button
                    onClick={() => { setMenuOpen(false); navigate('/edittransaction'); }}
                    style={menuItemStyle}
                    onMouseOver={(e) => e.target.style.background = "#f8f9fa"}
                    onMouseOut={(e) => e.target.style.background = "none"}
                  >
                    Edit Transactions
                  </button>
                  <div style={{ borderTop: "1px solid #eee", margin: "4px 0" }} />
                </div>
              )}
              {/* Sign Out */}
              <button
                onClick={handleSignOut}
                style={{
                  width: "100%",
                  padding: "12px 18px",
                  border: "none",
                  background: "none",
                  textAlign: "left",
                  cursor: "pointer",
                  fontSize: 16,
                  color: "#7b2222",
                  fontWeight: 600,
                  transition: "background 0.2s"
                }}
                onMouseOver={(e) => e.target.style.background = "#ffe6e6"}
                onMouseOut={(e) => e.target.style.background = "none"}
              >
                Sign Out
              </button>
            </div>
          )}
        </div>
      </div>
    </header>
  );
};

export default Header;
