import React from 'react';

const PrintTeluguReceipt = ({ receiptData, onClose }) => {
  const handlePrint = () => {
    const printContent = document.getElementById('telugu-receipt-content');
    const printWindow = window.open('', '_blank');

    printWindow.document.write(`
      <!DOCTYPE html>
      <html>
        <head>
          <title>Telugu Receipt</title>
          <meta charset="UTF-8">
          <style>
            @import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Telugu:wght@400;600;700&display=swap');
            @import url('https://fonts.googleapis.com/css2?family=Kalam:wght@300;400;700&display=swap');
            
            body {
              font-family: 'Noto Sans Telugu', Arial, sans-serif;
              margin: 0;
              padding: 20px;
              background: white;
              color: #000;
            }
            
            .telugu-receipt {
              max-width: 400px;
              margin: 0 auto;
              background: white;
              padding: 20px;
              border: 1px dashed #ccc;
            }
            
            .header {
              text-align: center;
              margin-bottom: 20px;
              color: #1e40af;
            }
            
            .temple-name {
              font-size: 16px;
              font-weight: 700;
              margin-bottom: 8px;
              line-height: 1.2;
            }
            
            .temple-address {
              font-size: 12px;
              margin-bottom: 5px;
            }
            
            .phone-number {
              font-size: 12px;
            }
            
            .receipt-line {
              display: flex;
              justify-content: space-between;
              margin-bottom: 15px;
              align-items: baseline;
            }
            
            .receipt-number {
              color: #dc2626;
              font-weight: bold;
              font-size: 16px;
            }
            
            .date-section {
              color: #2563eb;
              font-family: 'Kalam', cursive;
              font-size: 14px;
              transform: rotate(1deg);
            }
            
            .customer-section {
              color: #2563eb;
              font-family: 'Kalam', cursive;
              font-size: 14px;
              margin-bottom: 15px;
              transform: rotate(-0.5deg);
              line-height: 1.6;
            }
            
            .underline {
              text-decoration: underline;
            }
            
            .service-line {
              display: flex;
              align-items: center;
              margin-bottom: 20px;
              color: #2563eb;
              font-size: 14px;
              transform: rotate(-0.3deg);
            }
            
            .service-blank {
              flex: 1;
              border-bottom: 1px solid #666;
              height: 20px;
              margin: 0 15px;
              position: relative;
            }
            
            .service-value {
              position: absolute;
              bottom: -2px;
              left: 10px;
              font-family: 'Kalam', cursive;
              font-size: 14px;
              transform: rotate(0.5deg);
            }
            
            .puja-details {
              color: #2563eb;
              font-family: 'Kalam', cursive;
              font-size: 14px;
              margin-bottom: 20px;
              transform: rotate(0.5deg);
            }
            
            .puja-location {
              margin-left: 120px;
              transform: rotate(-1deg);
              margin-top: 10px;
            }
            
            .amount-section {
              display: flex;
              justify-content: space-between;
              align-items: end;
              margin-top: 30px;
            }
            
            .amount {
              font-size: 20px;
              font-weight: bold;
              transform: rotate(-1deg);
            }
            
            .amount-value {
              border-bottom: 2px solid black;
              padding: 2px 10px;
            }
            
            .signature {
              font-family: 'Kalam', cursive;
              transform: rotate(2deg);
              text-align: right;
            }
            
            .signature-line {
              border-bottom: 1px solid black;
              padding-bottom: 2px;
              font-size: 14px;
              min-width: 80px;
              text-align: center;
            }
            
            @media print {
              body { margin: 0; padding: 10px; }
              .telugu-receipt { border: 1px dashed #ccc; }
            }
          </style>
        </head>
        <body>
          <div class="telugu-receipt">
            <!-- Decorative border top -->
            <div style="border-top: 2px dashed #999; margin-bottom: 15px;"></div>
            
            <!-- Header -->
            <div class="header">
              <div class="temple-name">${'   '}</div>
              <div class="temple-address">${'  ,  , .'}</div>
              <div class="phone-number"> : ${'23833837'}</div>
            </div>
            
            <!-- Receipt number and date -->
            <div class="receipt-line">
              <div>
                <span style="color: #1e40af; font-size: 14px;">.</span>
                <span class="receipt-number">${receiptData.receipt_no}</span>
              </div>
              <div class="date-section"> ${receiptData.transaction_date}</div>
            </div>
            
            <!-- Customer details -->
            <div class="customer-section">
              <div style="margin-bottom: 10px;">
                 : / <span class="underline">${receiptData.devotee_name_telugu || receiptData.name || ''}</span>
              </div>
            </div>
            
            <!-- Service line -->
            <div class="service-line">
              <span></span>
              <div class="service-blank">
                ${receiptData.nakshatram_telugu || receiptData.nakshatram}
              </div>
              <span></span>
              <div class="service-blank">
                ${receiptData.gothram_telugu || receiptData.gothram ? `<span class="service-value" style="transform: rotate(-0.5deg);">${receiptData.gothram_telugu || receiptData.gothram}</span>` : ''}
              </div>
            </div>
            
            <!-- Puja details -->
            <div class="puja-details">
              <div>
                  
                <span style={{textDecoration: 'underline'}}>
                  {receiptData.description_telugu || receiptData.description}, 
                  {receiptData.seva_dates && receiptData.seva_dates.length > 0 ? receiptData.seva_dates.map(date => formatDate(date)).join(', '): receiptData.day}
                </span>
              </div>
              
            </div>
            
            <!-- Amount section -->
            <div class="amount-section">
              <div class="amount">
                . <span class="amount-value">${receiptData.amount || ''}/-</span>
              </div>
              <div class="signature">
                <div class="signature-line">${receiptData.signature || ''}</div>
              </div>
            </div>
          </div>
        </body>
      </html>
    `);

    printWindow.document.close();
    printWindow.focus();
    printWindow.print();
    printWindow.close();
  };

  const formatDate = (dateString) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleDateString('te-IN');
  };

  const containerStyle = {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0,0,0,0.7)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 1000,
    fontFamily: "'Noto Sans Telugu', Arial, sans-serif"
  };

  const modalStyle = {
    backgroundColor: 'white',
    borderRadius: '12px',
    padding: '20px',
    maxWidth: '500px',
    width: '90%',
    maxHeight: '90vh',
    overflow: 'auto',
    boxShadow: '0 10px 30px rgba(0,0,0,0.3)'
  };

  const buttonContainerStyle = {
    display: 'flex',
    gap: '10px',
    justifyContent: 'center',
    marginTop: '20px'
  };

  const buttonStyle = {
    padding: '10px 20px',
    borderRadius: '6px',
    border: 'none',
    fontWeight: '600',
    cursor: 'pointer',
    fontSize: '14px'
  };

  return (
    <div style={containerStyle} onClick={onClose}>
      <div style={modalStyle} onClick={(e) => e.stopPropagation()}>
        <div id="telugu-receipt-content">
          <div style={{
            maxWidth: '400px',
            margin: '0 auto',
            background: 'white',
            padding: '20px',
            fontFamily: "'Noto Sans Telugu', Arial, sans-serif",
            backgroundImage: 'linear-gradient(to bottom, #f9f9f9 0%, #ffffff 100%)',
            boxShadow: '0 4px 8px rgba(0,0,0,0.1)'
          }}>
            {/* Decorative border top */}
            <div style={{ borderTop: '2px dashed #999', marginBottom: '15px' }}></div>

            {/* Header */}
            <div style={{
              textAlign: 'center',
              marginBottom: '20px',
              color: '#1e40af'
            }}>
              <div style={{
                fontSize: '16px',
                fontWeight: '700',
                marginBottom: '8px',
                lineHeight: '1.2'
              }}>
                {'   '}
              </div>
              <div style={{
                fontSize: '12px',
                marginBottom: '5px'
              }}>
                {'  ,  , .'}
              </div>
              <div style={{ fontSize: '12px' }}>
                 : {'23833837'}
              </div>
            </div>

            {/* Receipt number and date */}
            <div style={{
              display: 'flex',
              justifyContent: 'space-between',
              marginBottom: '15px',
              alignItems: 'baseline'
            }}>
              <div>
                <span style={{ color: '#1e40af', fontSize: '14px' }}>.</span>
                <span style={{
                  color: '#dc2626',
                  fontWeight: 'bold',
                  fontSize: '16px',
                  marginLeft: '8px',
                  transform: 'rotate(-1deg)',
                  display: 'inline-block'
                }}>
                  {receiptData.receipt_no}
                </span>
              </div>
              <div style={{
                color: '#2563eb',
                fontFamily: 'Kalam, cursive',
                fontSize: '14px',
                transform: 'rotate(1deg)'
              }}>
                 {formatDate(receiptData.transaction_date)}
              </div>
            </div>

            {/* Customer details */}
            <div style={{
              color: '#2563eb',
              fontFamily: 'Kalam, cursive',
              fontSize: '14px',
              marginBottom: '15px',
              transform: 'rotate(-0.5deg)',
              lineHeight: '1.6'
            }}>
              <div style={{ marginBottom: '10px' }}>
                : / <span style={{ textDecoration: 'underline' }}>
                  {receiptData.devotee_name_telugu || receiptData.name || ''}
                </span>
              </div>

            </div>

            {/* Service line */}
            <div style={{
              display: 'flex',
              alignItems: 'center',
              marginBottom: '20px',
              color: '#2563eb',
              fontSize: '14px',
              transform: 'rotate(-0.3deg)'
            }}>
              <span></span>
              <div style={{
                flex: 1,
                borderBottom: '1px solid #666',
                height: '20px',
                margin: '0 15px',
                position: 'relative'
              }}>
                {(receiptData.nakshatram_telugu || receiptData.nakshatram) && (
                  <span style={{
                    position: 'absolute',
                    bottom: '-2px',
                    left: '10px',
                    fontFamily: 'Kalam, cursive',
                    fontSize: '14px',
                    transform: 'rotate(0.5deg)'
                  }}>
                    {receiptData.nakshatram_telugu || receiptData.nakshatram}
                  </span>
                )}
              </div>
              <span></span>
              <div style={{
                flex: 1,
                borderBottom: '1px solid #666',
                height: '20px',
                marginLeft: '15px',
                position: 'relative'
              }}>
                {(receiptData.gothram_telugu || receiptData.gothram) && (
                  <span style={{
                    position: 'absolute',
                    bottom: '-2px',
                    left: '10px',
                    fontFamily: 'Kalam, cursive',
                    fontSize: '14px',
                    transform: 'rotate(-0.5deg)'
                  }}>
                    {receiptData.gothram_telugu || receiptData.gothram}
                  </span>
                )}
              </div>
            </div>

            {/* Puja details */}
            <div style={{
              color: '#2563eb',
              fontFamily: 'Kalam, cursive',
              fontSize: '14px',
              marginBottom: '20px',
              transform: 'rotate(0.5deg)'
            }}>
              <div>
                 : <span style={{ textDecoration: 'underline' }}>
                  {receiptData.description_telugu || receiptData.description},
                  {receiptData.seva_dates && receiptData.seva_dates.length > 0
                    ? receiptData.seva_dates.map(date => formatDate(date)).join(', ')
                    : receiptData.day}
                </span>
              </div>

            </div>

            {/* Amount section */}
            <div style={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'end',
              marginTop: '30px'
            }}>
              <div style={{
                fontSize: '20px',
                fontWeight: 'bold',
                transform: 'rotate(-1deg)'
              }}>
                . <span style={{
                  borderBottom: '2px solid black',
                  paddingBottom: '2px',
                  paddingLeft: '10px',
                  paddingRight: '10px'
                }}>
                  {receiptData.amount ? Number(receiptData.amount).toLocaleString('en-IN') : ''}/-
                </span>
              </div>
              <div style={{
                fontFamily: 'Kalam, cursive',
                transform: 'rotate(2deg)',
                textAlign: 'right'
              }}>
                <div style={{
                  borderBottom: '1px solid black',
                  paddingBottom: '2px',
                  fontSize: '14px',
                  minWidth: '80px',
                  textAlign: 'center'
                }}>
                  {receiptData.signature || ''}
                </div>
              </div>
            </div>
          </div>
        </div>

        <div style={buttonContainerStyle}>
          <button
            onClick={handlePrint}
            style={{
              ...buttonStyle,
              backgroundColor: '#7b2222',
              color: 'white'
            }}
          >
             Print Receipt
          </button>
          <button
            onClick={onClose}
            style={{
              ...buttonStyle,
              backgroundColor: '#6c757d',
              color: 'white'
            }}
          >
            Close
          </button>
        </div>
      </div>
    </div>
  );
};

export default PrintTeluguReceipt;