import React, { useState } from "react";

const initialForm = {
  transaction_type: "",
  payment_mode: "",
  amount: "",
  cheque_no: "",
  cheque_date: "",
  date: ""
};

const JournalForm = () => {
  const [form, setForm] = useState(initialForm);
  const [status, setStatus] = useState({ type: "", msg: "" });
  const [loading, setLoading] = useState(false);
  const [journalNo, setJournalNo] = useState(null);
  const [printData, setPrintData] = useState(null);

  const handleChange = (e) => {
    setForm(f => ({ ...f, [e.target.name]: e.target.value }));
  };

  // Clear all fields and hide print option
  const handleClear = () => {
    setForm(initialForm);
    setStatus({ type: "", msg: "" });
    setJournalNo(null);
    setPrintData(null); // This hides the print button
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setStatus({ type: "", msg: "" });
    setJournalNo(null);
    setLoading(true);

    try {
      const journalData = {
        transaction_type: form.transaction_type,
        code_no: form.transaction_type === "cashDrawn" ? "J1" : "J2",
        description: form.transaction_type === "cashDrawn" ? "Cash Drawn from Bank" : "Cash Deposit to Bank",
        amount: Number(form.amount),
        transaction_date: form.date,
        mode_of_payment: form.payment_mode
      };

      if (form.payment_mode === "cheque") {
        journalData.cheque_no = form.cheque_no;
        journalData.cheque_date = form.cheque_date;
      }

      const res = await fetch(`${process.env.REACT_APP_API_BASE}/journals`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(journalData)
      });

      if (!res.ok) {
        const err = await res.json();
        setStatus({ type: "error", msg: err.error || "Failed to save journal entry" });
        return;
      }

      const saved = await res.json();
      setJournalNo(saved.journal_no);
      setStatus({ type: "success", msg: `Journal entry saved successfully! Journal No: ${saved.journal_no}` });
      setPrintData({
        ...form,
        journal_no: saved.journal_no,
        description: form.transaction_type === "cashDrawn" ? "Cash Drawn from Bank" : "Cash Deposit to Bank"
      });
      
    } catch (err) {
      setStatus({ type: "error", msg: "Network error occurred" });
    } finally {
      setLoading(false);
      setTimeout(() => setStatus({ type: "", msg: "" }), 4000);
    }
  };

  // Print function
  const handlePrint = () => {
    if (!printData) return;

    const printContent = document.createElement('div');
    printContent.innerHTML = `
      <div style="font-family: Inter, Arial, sans-serif; padding: 20px; max-width: 400px; margin: auto;">
        <div style="text-align: center; margin-bottom: 20px;">
          <h2 style="margin: 0; color: #7b2222;">Sri Sringeri Shankar Math</h2>
          <p style="margin: 5px 0; font-size: 14px;">Motinagar, Hyderabad</p>
        </div>
        
        <div style="border-top: 2px solid #7b2222; border-bottom: 2px solid #7b2222; padding: 15px 0; margin: 15px 0;">
          <div style="display: flex; justify-content: space-between; margin-bottom: 10px;">
            <strong>Journal No:</strong> <span>${printData.journal_no}</span>
          </div>
          <div style="display: flex; justify-content: space-between; margin-bottom: 10px;">
            <strong>Date:</strong> <span>${new Date().toLocaleDateString()}</span>
          </div>
          <div style="display: flex; justify-content: space-between; margin-bottom: 10px;">
            <strong>Type:</strong> <span>${printData.description}</span>
          </div>
          <div style="display: flex; justify-content: space-between; margin-bottom: 10px;">
            <strong>Amount:</strong> <span style="font-size: 18px; font-weight: bold;">${printData.amount}</span>
          </div>
          <div style="display: flex; justify-content: space-between; margin-bottom: 10px;">
            <strong>Mode:</strong> <span>${printData.payment_mode}</span>
          </div>
          ${printData.payment_mode === 'cheque' ? `
            <div style="display: flex; justify-content: space-between; margin-bottom: 10px;">
              <strong>Cheque No:</strong> <span>${printData.cheque_no}</span>
            </div>
            <div style="display: flex; justify-content: space-between; margin-bottom: 10px;">
              <strong>Cheque Date:</strong> <span>${printData.cheque_date}</span>
            </div>
          ` : ''}
        </div>
        
        <div style="text-align: center; margin-top: 20px; font-size: 12px; color: #666;">
          <p>Journal entry processed successfully</p>
          <p>    </p>
        </div>
      </div>
    `;

    const originalContent = document.body.innerHTML;
    document.body.innerHTML = printContent.innerHTML;
    window.print();
    document.body.innerHTML = originalContent;
    window.location.reload();
  };

  // Styles
  const formStyle = {
    maxWidth: 600,
    margin: "2rem auto",
    padding: "2rem 2.2rem 1.5rem 2.2rem",
    background: "linear-gradient(120deg, #fff 80%, #f8f5f2 100%)",
    borderRadius: 16,
    boxShadow: "0 4px 32px rgba(123,34,34,0.09)",
    fontFamily: "Inter, Arial, sans-serif"
  };

  const fieldStyle = { marginBottom: 18 };
  const labelStyle = {
    display: "block",
    marginBottom: 6,
    fontWeight: 600,
    color: "#7b2222",
    fontSize: 15,
    letterSpacing: 0.2
  };

  const inputStyle = {
    width: "100%",
    padding: "8px 12px",
    border: "1px solid #ddd",
    borderRadius: 6,
    fontSize: 16,
    background: "#faf9f7",
    marginTop: 2,
    marginBottom: 2,
    transition: "border 0.2s",
    outline: "none",
    boxSizing: "border-box"
  };

  const selectStyle = { ...inputStyle, background: "#fff" };

  const buttonStyle = {
    background: loading ? "linear-gradient(90deg, #999, #bbb)" : "linear-gradient(90deg, #7b2222 70%, #a94442 100%)",
    color: "#fff",
    border: "none",
    borderRadius: 8,
    padding: "10px 28px",
    fontWeight: 600,
    fontSize: 17,
    letterSpacing: 0.5,
    cursor: loading ? "not-allowed" : "pointer",
    boxShadow: "0 2px 12px rgba(123,34,34,0.07)",
    transition: "background 0.2s",
    marginRight: 10
  };

  const clearButtonStyle = {
    ...buttonStyle,
    background: "linear-gradient(90deg, #6c757d 70%, #868e96 100%)",
    cursor: "pointer"
  };

  const printButtonStyle = {
    ...buttonStyle,
    background: "linear-gradient(90deg, #28a745 70%, #34ce57 100%)",
    cursor: "pointer"
  };

  return (
    <div style={{
      minHeight: "100vh",
      background: "linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%)",
      padding: "32px 0",
      fontFamily: "Inter, Arial, sans-serif"
    }}>
      <div style={formStyle}>
        {/* Header Section */}
        <div style={{ textAlign: "center", marginBottom: "2rem" }}>
          <div style={{
            width: "70px",
            height: "70px",
            background: "linear-gradient(135deg, #7b2222, #a94442)",
            borderRadius: "50%",
            margin: "0 auto 1rem auto",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            boxShadow: "0 6px 20px rgba(123,34,34,0.25)"
          }}>
            <div style={{ fontSize: "28px", color: "#fff", fontWeight: "bold" }}></div>
          </div>
          <h2 style={{
            color: "#7b2222",
            marginBottom: 8,
            fontWeight: 700,
            letterSpacing: 0.5,
            fontSize: "1.5rem"
          }}>
            Journal Entry
          </h2>
          <p style={{ color: "#666", fontSize: "1rem", margin: 0, opacity: 0.8 }}>
            Record cash transactions and bank transfers
          </p>
        </div>

        {/* Status Messages */}
        {status.msg && (
          <div style={{
            background: status.type === "success" ? "linear-gradient(90deg, #d4edda, #c3e6cb)" : "linear-gradient(90deg, #f8d7da, #f5c6cb)",
            color: status.type === "success" ? "#155724" : "#721c24",
            padding: "14px 18px",
            borderRadius: 10,
            marginBottom: 20,
            fontSize: 15,
            textAlign: "center",
            fontWeight: 600,
            border: status.type === "success" ? "1px solid #c3e6cb" : "1px solid #f5c6cb",
            boxShadow: status.type === "success" ? "0 2px 8px rgba(21,87,36,0.1)" : "0 2px 8px rgba(114,28,36,0.1)"
          }}>
            {status.type === "success" ? "" : ""} {status.msg}
          </div>
        )}

        <form onSubmit={handleSubmit}>
          {/* Transaction Type */}
          <div style={fieldStyle}>
            <label style={labelStyle}>Transaction Type:</label>
            <select
              name="transaction_type"
              value={form.transaction_type}
              onChange={handleChange}
              required
              style={selectStyle}
            >
              <option value="">Select Transaction Type</option>
              <option value="cashDrawn"> Cash Drawn from Bank</option>
              <option value="cashDeposit"> Cash Deposit to Bank</option>
            </select>
          </div>

          {/* Payment Mode */}
          <div style={fieldStyle}>
            <label style={labelStyle}>Payment Mode:</label>
            <select
              name="payment_mode"
              value={form.payment_mode}
              onChange={handleChange}
              required
              style={selectStyle}
            >
              <option value="">Select Payment Mode</option>
              <option value="cash">Cash</option>
              <option value="cheque">Cheque</option>
              <option value="UPI/Online">UPI</option>
            </select>
          </div>

          {/* Cheque Details - Only if mode is cheque */}
          {form.payment_mode === "cheque" && (
            <>
              <div style={fieldStyle}>
                <label style={labelStyle}>Cheque No:</label>
                <input
                  type="text"
                  name="cheque_no"
                  value={form.cheque_no}
                  onChange={handleChange}
                  required
                  style={inputStyle}
                  placeholder="Enter cheque number"
                />
              </div>
              <div style={fieldStyle}>
                <label style={labelStyle}>Cheque Date:</label>
                <input
                  type="date"
                  name="cheque_date"
                  value={form.cheque_date}
                  onChange={handleChange}
                  required
                  style={inputStyle}
                />
              </div>
            </>
          )}

          {/* Amount */}
          <div style={fieldStyle}>
            <label style={labelStyle}>Amount:</label>
            <input
              type="number"
              name="amount"
              value={form.amount}
              onChange={handleChange}
              required
              min="0"
              step="0.01"
              style={inputStyle}
              placeholder="Enter amount"
            />
          </div>

          {/* Transaction Date */}
          <div style={fieldStyle}>
            <label style={labelStyle}>Transaction Date:</label>
            <input
              type="date"
              name="date"
              value={form.date}
              onChange={handleChange}
              required
              style={inputStyle}
            />
          </div>

          {/* Buttons */}
          <div style={{ display: "flex", gap: "10px", flexWrap: "wrap" }}>
            <button
              type="submit"
              disabled={loading}
              style={buttonStyle}
            >
              {loading ? "Saving Journal..." : "Save Journal"}
            </button>

            <button
              type="button"
              onClick={handleClear}
              style={clearButtonStyle}
            >
              Clear All
            </button>

            {/* Show print button only when printData exists */}
            {printData && (
              <button
                type="button"
                onClick={handlePrint}
                style={printButtonStyle}
              >
                Print Journal
              </button>
            )}
          </div>
        </form>

        {journalNo && (
          <div style={{
            textAlign: "center",
            marginTop: 20,
            padding: "12px 20px",
            background: "linear-gradient(90deg, #e8f5e8, #d4edda)",
            borderRadius: 10,
            border: "1px solid #c3e6cb"
          }}>
            <span style={{
              color: "#155724",
              fontWeight: 700,
              fontSize: 16
            }}>
              Journal No: {journalNo}
            </span>
          </div>
        )}

        {/* Footer */}
        <div style={{
          textAlign: "center",
          marginTop: "2rem",
          paddingTop: "1.5rem",
          borderTop: "1px solid #eee"
        }}>
          <p style={{
            color: "#666",
            fontSize: "13px",
            margin: 0,
            opacity: 0.7
          }}>
            Journal entries are recorded for cash and bank balance transfers
          </p>
        </div>
      </div>
    </div>
  );
};

export default JournalForm;
